import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard.vue'
import Settings from '@/components/Settings.vue'
import Activity from '@/components/Activity.vue'
import Configuration from '@/components/Configuration.vue'
import Loading from '@/components/Loading.vue'
import STORE from '@/store'
// import Template from '@/components/EwingTemplate.vue'

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    redirect: '/dashboard/no_node_selected'
  }, {
    path: '/dashboard/:nodeId',
    name: 'dashboard',
    component: Dashboard
  }, {
    path: '/settings',
    name: 'settings',
    component: Settings
  }, {
    path: '/activity',
    name: 'activity',
    component: Activity
  }, {
    path: '/configuration',
    name: 'configuration',
    component: Configuration
  }, {
    path: '/loading',
    name: 'loading',
    component: Loading
  }, {
    path: '*',
    redirect: '/dashboard'
  }]
})

router.beforeEach((to, from, next) => {
  if (process.env.NODE_ENV !== 'production') console.log('Router Change', from, to)
  if (to.name === 'dashboard' && (to.params.nodeId !== from.params.nodeId)) {
    STORE.dispatch('setAsyncSelectedNodeId', to.params.nodeId)
  }
  next()
})

export default router



