import { server } from '@/js/axios.js'
import * as account from '@/js/account'

// ============= Helper =============
const weekday = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']
const month = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'June', 'July', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']
const getTwoDigit = (time) => {
  let s = time.toString()
  return s.length < 2 ? '0' + s : s
}
/* eslint-disable no-unneeded-ternary */
const getCustomLocaleTime = (date) => {
  let hours = date.getHours()
  let minutes = date.getMinutes()
  let ampm = hours >= 12 ? 'PM' : 'AM'
  hours = hours % 12
  hours = hours ? hours : 12 // the hour '0' should be '12'
  minutes = minutes < 10 ? '0' + minutes : minutes
  const strTime = getTwoDigit(hours) + ':' + getTwoDigit(minutes) + ' ' + ampm
  return strTime
}

const state = {
  notifications: null,
  notificationsByDate: null,
  notificationsByNode: null,
  notificationsBySensor: null,
  notificationsByDateAndTime: null,
  notificationsByDateAndTimeAndNode: null
}

const getters = {
  getNotifications (state) { return state.notifications },
  getNotificationsByDate (state) { return state.notificationsByDate },
  getNotificationsByNode (state) { return state.notificationsByNode },
  getNotificationsBySensor (state) { return state.notificationsBySensor },
  getNotificationsByDateAndTime (state) { return state.notificationsByDateAndTime },
  getNotificationsByDateAndTimeAndNode (state) { return state.notificationsByDateAndTimeAndNode }
}

const mutations = {
  setNotifications (state, payload) {
    if (process.env.NODE_ENV !== 'production') console.log('setNotifications')
    state.notifications = payload
  },
  parseNotifications (state, payload) {
    if (process.env.NODE_ENV !== 'production') console.log('parseNotifications')
    if (payload.length === 0) return
    state.notificationsByDate = {}
    state.notificationsByNode = {}
    state.notificationsBySensor = {}
    state.notificationsByDateAndTime = {}
    state.notificationsByDateAndTimeAndNode = {}
    for (let n of payload) {
      const d = new Date(n.createdAt)
      const dateString = `${weekday[d.getDay()]}, ${month[d.getMonth()]} ${d.getDate()}, ${d.getFullYear()}`
      const timeString = getCustomLocaleTime(d)
    // const timeNode =
      if (!(dateString in state.notificationsByDate)) {
        state.notificationsByDate[dateString] = [n]
        state.notificationsByDateAndTime[dateString] = { [timeString]: [n] }
        state.notificationsByDateAndTimeAndNode[dateString] = { [timeString]: { [n.nodeId]: [n] } }
      } else {
        state.notificationsByDate[dateString].push(n)
        if (!(timeString in state.notificationsByDateAndTime[dateString])) {
          state.notificationsByDateAndTime[dateString][timeString] = [n]
          state.notificationsByDateAndTimeAndNode[dateString][timeString] = { [n.nodeId]: [n] }
        } else {
          state.notificationsByDateAndTime[dateString][timeString].push(n)
          if (!(n.nodeId in state.notificationsByDateAndTimeAndNode[dateString][timeString])) {
            state.notificationsByDateAndTimeAndNode[dateString][timeString][n.nodeId] = [n]
          } else {
            state.notificationsByDateAndTimeAndNode[dateString][timeString][n.nodeId].push(n)
          }
        }
      }
      if (!(n.nodeId in state.notificationsByNode)) state.notificationsByNode[n.nodeId] = [n]
      else state.notificationsByNode[n.nodeId].push(n)
      if (!(n.sensor in state.notificationsBySensor)) state.notificationsBySensor[n.sensor] = [n]
      else state.notificationsBySensor[n.sensor].push(n)
    }
  },
  updateNotification (state, noti) {
    const d = new Date(noti.createdAt)
    const dateString = `${weekday[d.getDay()]}, ${month[d.getMonth()]} ${d.getDate()}, ${d.getFullYear()}`
    const timeString = getCustomLocaleTime(d)
    for (let i = 0; i < state.notificationsByDateAndTimeAndNode[dateString][timeString][noti.nodeId].length; i++) {
      if (state.notificationsByDateAndTimeAndNode[dateString][timeString][noti.nodeId][i].id === noti.id) {
        state.notificationsByDateAndTimeAndNode[dateString][timeString][noti.nodeId][i].dismissed = noti.dismissed
      }
    }
  }
}

const actions = {
  updateAsyncNotification ({ commit }, noti) { commit('updateNotification', noti) },
  setAsyncNotifications ({ commit }, payload) {
    server.get('/notifications/all')
          .then((res) => {
            commit('setNotifications', res.data)
            commit('parseNotifications', res.data)
          })
          .catch((err) => {
            if (process.env.NODE_ENV !== 'production') console.log(err.response)
            account.errorHandler(err)
          })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



