<template>
<v-dialog v-model="showDialog"
          lazy
          absolute
          persistent
          :fullscreen="fullscreen"
          max-width="800px">
  <!-- <v-btn primary dark slot="activator">Open Dialog</v-btn> -->
  <v-card>
    <div class="text-xs-right grey darken-2 pa-1">
      <v-icon v-if="fullscreen"
              class="pointer"
              dark
              @click="fullscreen = false">fa-minus-square-o</v-icon>
      <v-icon v-else
              class="pointer"
              dark
              @click="fullscreen = true">check_box_outline_blank</v-icon>
      <v-icon class="pointer"
              dark
              @click="done()">close</v-icon>
    </div>
    <!-- <v-system-bar status
                  dark>
      <v-spacer></v-spacer>
      <v-icon v-if="fullscreen"
              class="pointer"
              @click="fullscreen = false">fa-minus-square-o</v-icon>
      <v-icon v-else
              class="pointer"
              @click="fullscreen = true">check_box_outline_blank</v-icon>
      <v-icon class="pointer"
              @click="done()">close</v-icon>
    </v-system-bar> -->
    <v-stepper v-model="step"
               dark>
      <v-stepper-header>
        <v-stepper-step step="1"
                        :complete="step > 1">Intro</v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step step="2"
                        :complete="step > 2">Configure Node<small>Optional</small></v-stepper-step>
        <v-divider></v-divider>
        <v-stepper-step step="3">Done</v-stepper-step>
      </v-stepper-header>
      <v-stepper-content step="1"
                         class="text-xs-right">
        <v-card class="teal lighten-2 mb-2 text-xs-left subheading px-3 py-2 stepper-card"
                :height="cardHeight">
          <div class="text-xs-left mb-1 font23">
            Let's get started by adding a device to your account.
            <!-- <br> -->
            <!-- <small>(If you do not have a device with us yet, please skip this section.)</small> -->
          </div>
          <br>
          <div>Please follow the instructions in order:</div>
          <br>
          <div class="paragraph-2">1. Please turn on the device by using the power button on the side.</div>
          <div class="paragraph-2">2. Change your current
            <span class="black--text"
                  style="font-weight: 900">WiFi Connection</span> to
            <span class="black--text"
                  style="font-weight: 900">{{getCompanyName}}-Device.</span>
          </div>
          <div class="paragraph-2">
            <span class="black--text"
                  style="font-weight: 900">&nbsp;&nbsp;&nbsp;IMPORTANT:</span> Do not refresh the page.</div>
          <br>
          <div>Next, we will configure the device using your network credentials.</div>
        </v-card>
        <v-btn class="red lighten-3 white--text"
               small
               style="width: 80px;"
               @click.native="next()">Next</v-btn>
      </v-stepper-content>
      <v-stepper-content step="2"
                         class="text-xs-right">
        <v-card class="teal lighten-2 mb-2 text-xs-left subheading px-3 py-2 stepper-card"
                :height="cardHeight">
          <div class="title text-xs-center mb-2 mt-1">Configure your Device</div>
          <div class="paragraph-1 text-xs-center">Select the WiFi name you want to connect to:</div>
          <div class="text-xs-center">
            <span v-if="wifiList.length === 0">
              <v-progress-circular indeterminate
                                   class="red--text"></v-progress-circular>
            </span>
            <span v-else>
              <v-chip v-for="(wifi,i) in wifiList"
                      :key="i"
                      label
                      @click="ssid = wifi"
                      class="deep-orange darken-1 white--text wifiBox">
                {{wifi}}
              </v-chip>
            </span>
          </div>
          <v-layout row
                    wrap>
            <v-flex xs12>
              <v-layout row
                        wrap>
                <v-flex xs5
                        class="text-xs-right"><b>SSID:</b></v-flex>
                <v-flex xs7
                        class="text-xs-left"
                        style="padding-left: 6px;"><input type="text"
                         id="ssid"
                         name="ssid"
                         v-model="ssid"></v-flex>
              </v-layout>
            </v-flex>
            <v-flex xs12>
              <v-layout row
                        wrap>
                <v-flex xs5
                        class="text-xs-right"><b>PASSWORD:</b></v-flex>
                <v-flex xs7
                        class="text-xs-left"
                        style="padding-left: 6px;"><input type="password"
                         name="passwd"
                         v-model="passwd"
                         @keyup.enter="postWifiCredential()"></v-flex>
              </v-layout>
            </v-flex>
          </v-layout>
          <div class="text-xs-center">{{errorMessage}}</div>
          <div class="text-xs-center"
               v-if="isConnecting">
            <v-progress-circular :size="90"
                                 :width="15"
                                 :rotate="-90"
                                 :value="configuringProgress"
                                 class="mt-3"
                                 color="red">
            </v-progress-circular>
            <!-- <span v-for="i in connectDotValue%4">.</span>
            <span>{{connectText}}</span>
            <span v-for="i in connectDotValue%4">.</span> -->
          </div>

          <div class="text-xs-center">
            <span v-if="hasConnected">Your device has been configured!</span>
            <v-btn v-else
                   small
                   class="red white--text"
                   v-show="!isConnecting"
                   @click="postWifiCredential()">configure</v-btn>
            <!-- {{hasConnected}} -->
            <!-- <v-btn v-if="hasConnected"
                   small
                   class="red white--text"
                   @click="rebootDevice()">Reboot</v-btn> -->
          </div>
        </v-card>
        <v-btn dark
               small
               @click.native="previous()">Previous</v-btn>
        <v-btn class="red lighten-3 white--text"
               small
               style="width: 80px;"
               @click.native="next()">Next</v-btn>
      </v-stepper-content>
      <v-stepper-content step="3"
                         class="text-xs-right">
        <v-card class="teal lighten-2 mb-2 text-xs-left subheading px-3 py-2 stepper-card"
                :height="cardHeight">
          <div class="font23 text-xs-left mb-2">&#x1F64C; Congratulations &#x1F64C;</div>
          <div>You have successfully configured your Device.</div>
          <div>NOTE: Please reconnect to your home/office network if you haven't automatically been reconnected.</div>
          <br>
          <div>You can now view your device under Devices in the left Menu.</div>
        </v-card>
        <v-btn dark
               small
               @click.native="previous()">Previous</v-btn>
        <v-btn class="cyan white--text"
               small
               @click.native="done()">Done</v-btn>
      </v-stepper-content>
    </v-stepper>
  </v-card>
</v-dialog>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import { device } from '@/js/axios.js'
import qs from 'qs'
export default {
  name: 'ConfigureNode',
  data() {
    return {
      step: 1,
      configNodeUrl: (process.env.NODE_ENV === 'production' ? 'http://192.168.4.1' : 'http://127.0.0.1:3000/configureNode.html'),
      wifiList: [],
      getWifiListInterval: '',
      ssid: '',
      passwd: '',
      fullscreen: false,
      isConnecting: false,
      connectDotValue: 1,
      connectDotInterval: '',
      connectText: '',
      hasConnected: false,
      showDialog: this.getConfigureNodeDialog,
      cardHeight: '300px',
      configuringProgressInterval: '',
      configuringProgress: 0,
      errorMessage: ''
    }
  },
  created() {},
  mounted() {},
  watch: {
    step(val) {
      if (val === 2) {
        this.getWifiList()
        this.getWifiListInterval = setInterval(this.getWifiList, 10000)
      } else { clearInterval(this.getWifiListInterval) }
    },
    isConnecting(val) {
      if (val) {
        this.connectDotInterval = setInterval(() => { this.connectDotValue += 1 }, 300)
        this.configuringProgressInterval = setInterval(() => { this.configuringProgress += 11 }, 1000)
      } else {
        clearInterval(this.connectDotInterval)
        clearInterval(this.configuringProgressInterval)
        this.configuringProgress = 0
      }
      setTimeout(() => {
        this.connectText = ''
        this.isConnecting = false
        this.step = 3
      }, 10000)
    },
    getConfigureNodeDialog(val) {
      if (val) this.setAsyncDrawer(false)
      this.showDialog = val
    },
    showDialog(val) { this.setAsyncConfigureNodeDialog(val) },
    fullscreen(val) {
      const sysBar = 32
      const stepper = 87
      const dialogBorder = 80
      this.cardHeight = val ? `${this.getViewport.y - dialogBorder - stepper - sysBar}px` : '300px'
    }
  },
  computed: {
    ...mapGetters([
      'getCompanyName',
      'getWelcomeDialog',
      'getConfigureNodeDialog',
      'getViewport'
    ]),
    ...mapGetters('user', ['getUserData', 'getToken'])
  },
  methods: {
    ...mapActions([
      'setAsyncActivePage',
      'setAsyncWelcomeDialog',
      'setAsyncConfigureNodeDialog',
      'setAsyncDrawer'
    ]),
    ...mapActions('user', ['setAsyncUserData']),
    previous() {
      if (this.step < 1) this.step = 1
      else this.step -= 1
    },
    next() { this.step += 1 },
    done() {
      this.setAsyncConfigureNodeDialog(false)
      this.step = 1
      this.fullscreen = false
      this.hasConnected = false
      this.isConnecting = false
      this.setAsyncUserData()
    },
    getWifiList() {
      device.get('/getWifiList')
        .then((res) => { this.wifiList = res.data.split(',') })
        .catch(err => console.trace(err))
    },
    postWifiCredential() {
      if (this.ssid === '') {
        this.errorMessage = 'Please select a valid SSID'
        return
      } else if (this.passwd === '') {
        this.errorMessage = 'Please provide a password'
        return
      } else this.errorMessage = ''
      this.isConnecting = true
      this.connectText = 'Configuring'
      this.hasConnected = false
      clearInterval(this.getWifiListInterval)
      device.get(`/getWifiCredential?${qs.stringify({ ssid: this.ssid, pwd: this.passwd, api_token: this.getToken, reboot: true })}`)
        .then((res) => {
          this.isConnecting = false
          this.hasConnected = true
        })
        .catch(err => console.trace(err))
    },
    rebootDevice() {
      this.isConnecting = true
      this.connectText = 'Rebooting the device'
      device.get('/rebootDevice')
        .then((res) => {
          this.isConnecting = false
        })
        .catch(err => console.trace(err))
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="sass" scoped>
.border
    border: 1px dashed grey
.pointer
    cursor: pointer
.stepper-card
    font-family: 'Andale Mono', 'Arial'
    color: white
    overflow: hidden
    overflow-y: scroll
    .paragraph-1
        text-indent: 10px
    .paragraph-2
        text-indent: 40px
        font-size: 17px
.wifiBox
    transition: all 0.5s ease
    cursor: pointer
    &:hover
        box-shadow: 2px 2px 2px 0px rgba(247, 247, 247, 0.5)
input[type=password], input[type=text]
    width: 140px
    padding: 2px
    font-size: 14px
    display: inline-block
    border: 1px solid #ccc
    box-shadow: inset 0 1px 3px #ddd
    border-radius: 4px
    vertical-align: middle
    box-sizing: border-box
input[type=password]:focus, input[type=text]:focus
    outline: 0
</style>



