import Vue from 'vue'
import Vuex from 'vuex'
// import Cookies from 'js-cookie'

// Shared Getters Mutations Actions
import * as getters from '@/store/getters'
import * as mutations from '@/store/mutations'
import * as actions from '@/store/actions'

import user from '@/store/modules/user'
import { Theme } from '@/style'

Vue.use(Vuex)

const store = new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  state: {
    selectedNodeId: 'no_node_selected',
    activePage: 'dashboard',
    snackBar: {
      timeout: 1000,
      horizontalPos: 'top',
      verticalPos: 'right',
      colorType: 'success',
      mode: 'multi-line',
      show: true,
      content: 'Welcome!'
    },
    companyName: 'SproutLabs',
    mini: true,
    drawer: true,
    welcomeDialog: false,
    configureNodeDialog: false,
    accountSettingDialog: false,
    mobileBreakPoint: 700,
    tooltipColor: Theme.tooltipColor
  },
  getters,
  mutations,
  actions,
  modules: { user }
})

export default store

if (process.env.NODE_ENV !== 'production') console.log('VueX created')



