export const setAsyncActivePage = ({ commit }, payload) => { if (payload !== '') commit('setActivePage', payload) }
export const setAsyncSelectedNodeId = ({ commit }, payload) => { commit('setSelectedNodeId', payload) }
export const updateAsyncSnackBar = ({ commit }, config) => { commit('updateSnackBar', config) }
export const setAsyncMini = ({ commit }, payload) => { commit('setMini', payload) }
export const setAsyncDrawer = ({ commit }, payload) => { commit('setDrawer', payload) }
export const setAsyncWelcomeDialog = ({ commit }, payload) => { commit('setWelcomeDialog', payload) }
export const setAsyncConfigureNodeDialog = ({ commit }, payload) => { commit('setConfigureNodeDialog', payload) }
export const setAsyncAccountSettingDialog = ({ commit }, payload) => { commit('setAccountSettingDialog', payload) }
export const setAsyncTooltipColor = ({commit}, payload) => { commit('setTooltipColor', payload) }



