<template>
<v-navigation-drawer app
                     :clipped="mini"
                     dark
                     :mini-variant.sync="mini"
                     :mobile-break-point="getMobileBreakPoint"
                     :persistent="getViewport.x > getMobileBreakPoint"
                     :temporary="getViewport.x <= getMobileBreakPoint"
                     v-model="drawer"
                     overflow>
  <v-list>
    <v-list-tile avatar>
      <v-list-tile-avatar style="cursor: pointer">
        <v-icon large
                class="white--text">account_circle</v-icon>
      </v-list-tile-avatar>
      <v-list-tile-content>
        <v-list-tile-title>
          {{ firstname | capitalize }}
        </v-list-tile-title>
      </v-list-tile-content>
      <v-list-tile-action>
        <v-btn icon
               @click.stop="setAsyncMini(!getMini)">
          <v-icon>chevron_left</v-icon>
        </v-btn>
      </v-list-tile-action>
    </v-list-tile>
  </v-list>
  <v-divider></v-divider>

  <v-list class="pt-0"
          dense>
    <v-list-group v-for="(item, i) in sideBarItems"
                  :key="i">
      <v-list-tile slot="item"
                   @click="item.path === 'dashboard' ? setAsyncActivePage({name: item.path, params:{nodeId: getSelectedNodeId}}) : setAsyncActivePage({name: item.path})">
        <v-tooltip right
                   :color="getTooltipColor">
          <v-list-tile-action slot="activator">
            <v-badge>
              <span>
                <v-icon :color="item.path === getActivePage ? 'blue' : ''">{{ item.icon }}</v-icon>
              </span>
            </v-badge>
          </v-list-tile-action>
          <span>{{item.title}}</span>
        </v-tooltip>
        <v-list-tile-content>
          <v-list-tile-title class="text--lighten-2"
                             :class="{'blue--text': item.path === getActivePage }">
            {{ item.title }}
          </v-list-tile-title>
        </v-list-tile-content>
      </v-list-tile>
    </v-list-group>
    <DeviceList></DeviceList>
  </v-list>
</v-navigation-drawer>
</template>

<script>
import {
  mapGetters,
  mapActions
} from 'vuex'
import deviceBattery from '@/components/DeviceBattery'
import DeviceList from './DeviceList'
import { emptyNodeNameString } from '@/js/transforms.js'

export default {
  name: 'NavigationDrawer',
  components: { deviceBattery, DeviceList },
  data() {
    return {
      mini: true,
      drawer: true,
      hoveredOnTile: -1,
      isEditing: -1,
      editNodeName: '',
      sideBarItems: [
        { title: 'Dashboard', icon: 'dashboard', path: 'dashboard' },
        { title: 'Activity', icon: 'add_alert', path: 'activity' },
        { title: 'Configuration', icon: 'settings', path: 'configuration' }
        // { title: 'Settings', icon: 'settings', path: 'settings' },
      ]
    }
  },
  created() { if (process.env.NODE_ENV !== 'production') console.log('NavigationDrawer.vue') },
  mounted() {
    if (this.getViewport.x < this.getMobileBreakPoint) this.setAsyncDrawer(false)
    if (this.$route.name) this.setAsyncActivePage({ name: this.$route.name })
  },
  computed: {
    ...mapGetters([
      'getSelectedNodeId',
      'getActivePage',
      'getMini',
      'getDrawer',
      'getTooltipColor',
      'getMobileBreakPoint',
      'getViewport'
    ]),
    ...mapGetters('user', ['getUserData']),
    firstname() {
      if (this.getUserData) return this.getUserData.firstname
      else return ''
    }
  },
  methods: {
    ...mapActions([
      'setAsyncActivePage',
      'setAsyncMini',
      'setAsyncDrawer'
    ]),
    ...mapActions('user/node', ['updateAsyncNodesInfo']),
    emptyNodeNameString
  },
  watch: {
    getDrawer(val) { this.drawer = val },
    drawer(val) { this.setAsyncDrawer(val) },
    getMini(val) { this.mini = val },
    mini(val) { this.setAsyncMini(val) }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="sass" scoped>
.border
  // border: 1px dashed grey

input
  border-bottom: 1px solid white
  height: 18px
  outline: none
</style>



