<template>
<v-app id="app"
       toolbar>
  <NavigationDrawer></NavigationDrawer>
  <v-toolbar app
             flat
             :clipped-left="getMini"
             fixed
             class="primary"
             dark>
    <v-toolbar-side-icon @click.stop="drawer=!drawer"></v-toolbar-side-icon>
    <v-toolbar-title>
      {{ getActivePage | capitalize }}
      <span v-if="getActivePage==='dashboard'&&getSelectedNodeId">
        : {{ getSelectedNodeId !== 'no_node_selected' ? getNodeInfoById(getSelectedNodeId).name : 'No Node Selected' }}
      </span>
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <!-- <v-btn icon>
      <v-icon v-badge="{ value:'9', overlap:true }"
              class="red--after">flag</v-icon>
    </v-btn> -->
    <v-tooltip bottom
               :color="getTooltipColor">
      <v-btn icon
             slot="activator"
             @click="setAsyncWelcomeDialog(true)">
        <v-icon>info</v-icon>
      </v-btn>
      <span>Introduction</span>
    </v-tooltip>
    <!-- <v-tooltip left
               :color="getTooltipColor">
      <v-btn icon
             slot="activator"
             @click="setAsyncAccountSettingDialog(true)">
        <v-icon>settings</v-icon>
      </v-btn>
      <span>Setting</span>
    </v-tooltip> -->
    <v-tooltip bottom
               :color="getTooltipColor">
      <v-btn icon
             slot="activator"
             @click="logout()">
        <v-icon>fa-sign-out</v-icon>
      </v-btn>
      <span>Logout</span>
    </v-tooltip>
  </v-toolbar>
  <main>
    <v-content>
      <v-container fluid
                   class="border pa-0 ">
        <v-layout row
                  wrap
                  class="border">
          <v-flex xs12>
            <transition name="slide-fade"
                        mode="out-in">
              <keep-alive>
                <router-view></router-view>
              </keep-alive>
            </transition>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </main>

  <configure-node></configure-node>
  <welcome></welcome>
  <account-setting></account-setting>

  <v-snackbar :top="getSnackBar.horizontalPos==='top'"
              :bottom="getSnackBar.horizontalPos==='bottom'"
              :right="getSnackBar.verticalPos==='right'"
              :left="getSnackBar.verticalPos==='left'"
              :timeout="getSnackBar.timeout"
              :color="getSnackBar.colorType"
              :multi-line="getSnackBar.mode==='multi-line'"
              :vertical="getSnackBar.mode==='vertical'"
              v-model="showSnackBar">
    <div class="subheading">{{ getSnackBar.content }}</div>
    <v-btn fab
           dark
           flat
           @click.native="showSnackBar=false">
      <v-icon>close</v-icon>
    </v-btn>
  </v-snackbar>
</v-app>
</template>

<script>
import {
  mapGetters,
  mapActions
} from 'vuex'
import configureNode from '@/components/ConfigureNode'
import welcome from '@/components/Welcome'
import accountSetting from '@/components/AccountSetting'
import NavigationDrawer from '@/components/NavigationDrawer'
import { emptyNodeNameString } from '@/js/transforms.js'
import * as account from '@/js/account'

export default {
  name: 'App',
  components: { configureNode, welcome, accountSetting, NavigationDrawer },
  data() {
    return {
      showSnackBar: true,
      drawer: true,
      sideBarItems: [
        { title: 'Dashboard', icon: 'dashboard', path: 'dashboard' },
        { title: 'Activity', icon: 'add_alert', path: 'activity' },
        { title: 'Configuration', icon: 'search', path: 'configuration' },
        // { title: 'Settings', icon: 'settings', path: 'settings' },
        { title: 'Devices', icon: 'view_module', path: '', items: [] }
      ]
    }
  },
  created() { if (process.env.NODE_ENV !== 'production') console.log('App.vue') },
  mounted() {},
  computed: {
    ...mapGetters([
      'getSelectedNodeId',
      'getActivePage',
      'getSnackBar',
      'getAccountSettingDialog',
      'getTooltipColor',
      'getDrawer',
      'getMini'
    ]),
    ...mapGetters('user', ['getUserData']),
    ...mapGetters('user/node', ['getNodeInfoById', 'getAllNodeId'])
  },
  methods: {
    ...mapActions([
      'updateAsyncSnackBar',
      'setAsyncDrawer',
      'setAsyncWelcomeDialog',
      'setAsyncAccountSettingDialog',
      'setAsyncConfigureNodeDialog'
    ]),
    ...mapActions('user/node', ['setAsyncLatestReading']),
    logout() { account.logout() },
    emptyNodeNameString
  },
  watch: {
    getUserData() {
      // set the node list on the side bar
      if (this.getAllNodeId.length === 0) this.setAsyncWelcomeDialog(true)
      if (this.getAllNodeId.length !== 0) this.setAsyncLatestReading(this.getAllNodeId)
      // this.setAsyncLatestReading()
    },
    showSnackBar(val) { this.updateAsyncSnackBar({ show: val }) },
    getSnackBar(val) { this.showSnackBar = val.show },
    getDrawer(val) { this.drawer = val },
    drawer(val) { this.setAsyncDrawer(val) }
  }
}
</script>

<style lang="stylus">
  //This will apply on entire environ
  @import '../node_modules/vuetify/src/stylus/settings/_colors'
  $theme := {
    primary: $blue.lighten-2
    accent: $blue-grey.darken-1
    secondary: $grey.darken-4
    info: $blue.lighten-1
    warning: $amber.darken-2
    error: $red.accent-3
    success: $green.lighten-2
  }
  @import '../node_modules/vuetify/src/stylus/main'
</style>

<style lang="sass">
@import url('https://fonts.googleapis.com/css?family=Quicksand')
html, body
  margin: 0

.border
  // border: 1px dashed grey

#app
  font-family: Quicksand, sans-serif
  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale
  color: #2c3e50

#mainContainer
  padding-top: 0px

.font23
    font-size: 23px

.slide-fade-enter
  transform: translateY(1300px)
.slide-fade-enter-active
  transition: all 0.8s ease
.slide-fade-enter-to
  transform: translateY(0px)
.slide-fade-leave
  transform: translateX(0px)
.slide-fade-leave-active
  transition: all 0.2s
.slide-fade-leave-to
  transform: translateX(2500px)
</style>



