// =============================================
// For Storing General User Node and Data
// =============================================
import Cookies from 'js-cookie'
import { server } from '@/js/axios.js'
import { emptyNodeNameString } from '@/js/transforms.js'
import notification from '@/store/modules/notification'
import node from '@/store/modules/node'
import * as account from '@/js/account'
import ROUTER from '@/router'

const state = {
  userData: null,
  token: Cookies.get('token')
}

const getters = {
  getUserData (state) { return state.userData },
  getToken (state) { return state.token }
}

const mutations = {
  setToken (state, payload) { state.token = payload },
  setUserData (state, payload) {
    if (process.env.NODE_ENV !== 'production') console.log('setUserData')
    state.userData = payload
  },
  setUpdatedNodeInfo (state, payload) {
    if (process.env.NODE_ENV !== 'production') console.log('setUpdatedNodeInfo')
    for (let updatedNode of payload) {
      for (let [i, node] of state.userData.nodes.entries()) {
        if (node.id === updatedNode.id) {
          state.userData.nodes.splice(i, 1, updatedNode)
          break
        }
      }
    }
  }
}

const actions = {
  setAsyncUserData ({ state, commit, rootState, dispatch }) {
    server.get('/users/getuser')
          .then((res) => {
            if (process.env.NODE_ENV !== 'production') console.log('setAsyncUserData')
            for (let el of res.data.nodes) { // Fix Null Name Issue
              if (!el.name) el.name = emptyNodeNameString(el.id)
              if (!el.groupName) el.groupName = 'Unassigned'
            }
            if (rootState.activePage === 'dashboard' && rootState.selectedNodeId === 'no_node_selected') {
              if (process.env.NODE_ENV !== 'production') console.log('Enter App with /')
              if (res.data.nodes.length !== 0) {
                ROUTER.replace({ name: 'dashboard', params: { nodeId: res.data.nodes[0].id } })
                commit('setSelectedNodeId', res.data.nodes[0].id, {root: true})
                res.data.nodes[0].active = true
              }
            }
            commit('setUserData', res.data)
            dispatch('node/setAsyncParseAllNode', {root: true})
            // commit('node/setAllNodeId', res.data.nodes)
            // commit('node/parseNodeByGroup', res.data.nodes)
            // commit('node/parseSensors', res.data.nodes) // Should be auto parse but cannot do that for now
          })
          .catch((err) => {
            if (process.env.NODE_ENV !== 'production') console.log(err.response)
            account.errorHandler(err)
          })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions,
  modules: { node, notification }
}



