<script>
import SensorBox from '@/components/SensorBox.vue'
import DashboardChart from '@/components/DashboardChart.vue'
import SensorAvatar from '@/components/SensorAvatar.vue'
import RecentNotificationBox from '@/components/RecentNotificationBox.vue'
// import { server } from '@/js/axios.js'
import {
  mapGetters,
  mapActions
} from 'vuex'

export default {
  name: 'Dashboard',
  components: { SensorBox, DashboardChart, SensorAvatar, RecentNotificationBox },
  data() {
    return {
      msg: 'Dashboard',
      nodeData: { id: 'no_data_found', moisture: [], sunlight: [], temperature: [], humidity: [], time: [] }
    }
  },
  created() {},
  mounted() { if (this.getUserData) this.init() },
  watch: {
    getUserData() { this.init() },
    getSelectedNodeId(val, oldVal) { if (val !== oldVal) this.init() },
    getAllNodeData(val) { this.init() }
  },
  computed: {
    ...mapGetters(['getSelectedNodeId', 'getToken', 'getMini', 'getDrawer', 'getActivePage', 'getTooltipColor']),
    ...mapGetters('user', ['getUserData']),
    ...mapGetters('user/node', ['getNodeInfoById', 'getAllSensors', 'getSensorOptions', 'getAllNodeData', 'getNodeDataById', 'getAllNodeId', 'getLastestReading']),
    selectedNode() { return this.getNodeInfoById(this.getSelectedNodeId) },
    pageActive() { return this.getActivePage === 'dashboard' }
  },
  methods: {
    ...mapActions(['setAsyncSelectedNodeId', 'updateAsyncSnackBar']),
    ...mapActions('user/node', ['setAsyncNodeData']),
    init() {
      if (process.env.NODE_ENV !== 'production') console.log('INIT')
      if (this.getSelectedNodeId === 'no_node_selected') { return } else {
        if (this.selectedNode.name !== 'no_node_found') this.setNodeData()
        else {
          this.setNodeData()
          this.updateAsyncSnackBar({ show: true, content: `Cannot find node with id-${this.getSelectedNodeId}`, colorType: 'error' })
        }
      }
    },
    setNodeData() {
      const id = this.getSelectedNodeId
      if (!(id in this.getAllNodeData)) { // Node data is not found in cache, load it
        this.setAsyncNodeData({ id: id, hour: 30 * 24 })
        this.nodeData = { id: 'loading_data', moisture: [], sunlight: [], temperature: [], humidity: [], time: [] }
      } else if (this.nodeData.id === 'loading_data') { // if this is waiting on loading, set it to the data in cache
        this.nodeData = this.getAllNodeData[id]
      } else {
        this.nodeData = this.getAllNodeData[id]
      }
    }
  }
}
</script>
