<template>
<v-layout row
          wrap
          class="mb-2 px-1">
  <v-flex xs12>
    <div class="text-xs-left elevation-1 white">
      <v-layout row
                wrap>
        <v-flex xs6
                class="border pa-0 pl-1">
          <v-btn flat
                 small
                 :outline="activeChartView === 0"
                 round
                 class="border mx-0 my-1 blue-grey"
                 style="width:80px; font-size: 11px; height:25px;"
                 @click.native="activeChartView = 0">Day</v-btn>
          <v-btn flat
                 small
                 :outline="activeChartView === 1"
                 round
                 class="border mx-0 my-1 blue-grey"
                 style="width:80px; font-size: 11px; height:25px;"
                 @click.native="activeChartView = 1">Week</v-btn>
          <v-btn flat
                 small
                 :outline="activeChartView === 2"
                 round
                 class="border mx-0 my-1 blue-grey"
                 style="width:80px; font-size: 11px; height:25px;"
                 @click.native="activeChartView = 2">Month</v-btn>
        </v-flex>
        <v-flex xs6
                class="border text-xs-right pa-0 pr-1">
          <v-btn v-for="(sensor, i) in getAllSensors"
                 :key="i"
                 small
                 class="border mx-1 my-1"
                 :flat="activeChartSensor !== sensor.value"
                 :outline="activeChartSensor !== sensor.value"
                 style="width:90px; font-size: 11px; height: 25px;"
                 :class="{[`${getSensorOptions[sensor.value].colorName}--text`]: activeChartSensor !== sensor.value, [`${getSensorOptions[sensor.value].colorName} white--text`]: activeChartSensor === sensor.value}"
                 @click.native="activeChartSensor = sensor.value">{{sensor.name | capitalize}}</v-btn>
        </v-flex>
        <v-flex xs12>
          <v-card>
            <transition name="expand">
              <v-progress-linear :indeterminate="true"
                                 v-if="loadingData"
                                 class="pa-0 ma-0 teal lighten-4"></v-progress-linear>
            </transition>
            <canvas id='canv'>Your browser does not support the HTML5 canvas tag.</canvas>
            <!-- <v-layout row
                      wrap>
              <v-flex xs12
                      sm6
                      class="px-4 text-xs-center text-sm-left">
                <b>{{dateStringObj.last}}</b>
              </v-flex>
              <v-flex xs12
                      sm6
                      class="px-4 text-xs-center text-sm-right">
                <b>{{dateStringObj.now}}</b>
              </v-flex>
            </v-layout> -->
          </v-card>
        </v-flex>
      </v-layout>
    </div>
  </v-flex>
</v-layout>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { newChart } from '@/js/chart.js'
import { dbSensorName } from '@/js/transforms.js'

export default {
  name: 'DashboardChart',
  components: {},
  props: {
    templateProp: {
      type: String,
      default: 'Template'
    },
    nodeData: {
      type: Object,
      default: {}
    },
    selectedNode: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      activeChartSensor: 'moisture',
      activeChartView: 0,
      loadingData: false,
      chart: null
    }
  },
  created() {},
  mounted() { this.createChart() },
  beforeDestory() {},
  watch: {
    activeChartSensor(val) { this.changeChartSensor(val) },
    activeChartView(val) { this.changeChartView(val) },
    getMini(val) { if (this.chart) setTimeout(() => this.chart.onResize(), 300) },
    getDrawer(val) { if (this.chart) setTimeout(() => this.chart.onResize(), 300) },
    nodeData(val, oldVal) { setTimeout(this.createChart, 500) },
    getActivePage(val) { setTimeout(this.createChart, 500) },
    getUserData: {
      handler() {
        if (!this.chart) return
        for (let s of this.getAllSensors) {
          this.chart.setGraphBadRange(s.value, this.selectedNode[`${dbSensorName(s.value)}Min`], this.selectedNode[`${dbSensorName(s.value)}Max`])
        }
      },
      deep: true
    }
  },
  computed: {
    ...mapGetters(['getMini', 'getDrawer', 'getActivePage']),
    ...mapGetters('user', ['getUserData']),
    ...mapGetters('user/node', ['getNodeInfoById', 'getAllSensors', 'getSensorOptions']),
    dateStringObj() {
      if (!this.nodeData) return 'No Data'
      else if (!(this.activeChartSensor in this.nodeData)) return 'No Data'
      else if (this.nodeData[this.activeChartSensor].length === 0) return ''
      let duration
      if (this.activeChartView === 0) duration = 1 // day
      else if (this.activeChartView === 1) duration = 7 // week
      else if (this.activeChartView === 2) duration = 30 // month
      const epocNow = this.nodeData[this.activeChartSensor][this.nodeData[this.activeChartSensor].length - 1].x
      const epocLast = epocNow - (86400000 * duration)
      return { now: this.epocToCustomString(epocNow), last: this.epocToCustomString(epocLast) }
    }
  },
  methods: {
    ...mapActions([]),
    createChart() {
      // Add check for try catch
      // add watchers for activePage change
      // add check if not active dashboard return
      //   console.log(document.getElementById('canv'))
      if (this.nodeData.id === 'loading_data' || this.getActivePage !== 'dashboard') {
        this.loadingData = true
        return
      }
      if (this.nodeData.id === 'no_data_found') {}
      if (process.env.NODE_ENV !== 'production') console.log('Draw Chart', this.selectedNode.id)
      this.loadingData = false
      this.chart = newChart('canv', this.activeChartView)
      for (let s of this.getAllSensors) {
        const graphName = s.value
        const opt = this.getSensorOptions[graphName]
        this.chart.pushPoints(graphName, this.nodeData[graphName])
        this.chart.setGraphBadRange(graphName, this.selectedNode[`${dbSensorName(graphName)}Min`], this.selectedNode[`${dbSensorName(graphName)}Max`])
        this.chart.setGraphColors(graphName, opt.goodLineColor, opt.goodAreaColor)
        this.chart.setBadColors(graphName, opt.badLineColor, opt.badAreaColor)
        this.chart.changeYAxis(graphName, opt.yMin, opt.yMax, opt.splitY, opt.unit)
      }
      this.chart.showGraph(this.activeChartSensor)
      this.chart.setMainGraph(this.activeChartSensor)
      this.chart.onResize()
    },
    // dateCalculation() {
    //   let dateEpocList = []
    //   const options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }
    //   for (let sensor of this.getAllSensors) {
    //     if (this.nodeData[sensor.value].length !== 0) {
    //       dateEpocList.push(this.nodeData[sensor.value][this.nodeData[sensor.value].length - 1].x)
    //     }
    //   }
    //   dateEpocList.sort()
    //   let dateStringList = dateEpocList.map(o => new Date(o).toLocaleDateString('en-US', options))
    //   console.log(dateStringList)
    // },
    nth(d) {
      if (d > 3 && d < 21) return 'th' // thanks kennebec
      switch (d % 10) {
        case 1:
          return 'st'
        case 2:
          return 'nd'
        case 3:
          return 'rd'
        default:
          return 'th'
      }
    },
    epocToCustomString(epoc) {
      let options
      options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', hour12: true }
      const date = new Date(epoc).toLocaleString('en-US', options)
      let stringDate = ''
      let listDate = date.split(', ')
      listDate[1] = listDate[1].split(' ')[0] + ' ' + listDate[1].split(' ')[1] + this.nth(listDate[1].split(' ')[1])
      if (this.activeChartView === 0 || this.activeChartView === 1) stringDate = listDate.join(' ')
      else if (this.activeChartView === 2) stringDate = listDate.slice(0, listDate.length - 2).join(' ')
      return stringDate
    },
    changeChartView(viewType) { this.chart.changeTimeWindow(viewType) },
    changeChartSensor(sensorType) {
      for (let sensor of this.getAllSensors) {
        if (this.activeChartSensor === sensor.value) {
          this.chart.showGraph(sensor.value)
          this.chart.setMainGraph(sensor.value)
        } else { this.chart.hideGraph(sensor.value) }
      }
      this.chart.onResize()
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="sass" scoped>
// .border
//     border: 1px dashed grey

#canv
  width: 100%
  height: 300px
  min-width: 250px

.expand-enter
  max-height: 0px
.expand-enter-active
  transition: max-height 0.5s ease-in
.expand-enter-to
  max-height: 20px
.expand-leave
  max-height: 20px
.expand-leave-active
  transition: max-height 1s ease-in
.expand-leave-to
  max-height: 0px
</style>



