export const getSelectedNodeId = (state) => { return state.selectedNodeId }
export const getActivePage = (state) => { return state.activePage }
export const getSnackBar = (state) => { return state.snackBar }
export const getCompanyName = (state) => { return state.companyName }
export const getMini = (state) => { return state.mini }
export const getDrawer = (state) => { return state.drawer }
export const getWelcomeDialog = (state) => { return state.welcomeDialog }
export const getConfigureNodeDialog = (state) => { return state.configureNodeDialog }
export const getAccountSettingDialog = (state) => { return state.accountSettingDialog }
export const getViewport = (state) => {
  const w = window
  const d = document
  const e = d.documentElement
  const g = d.getElementsByTagName('body')[0]
  const x = w.innerWidth || e.clientWidth || g.clientWidth
  const y = w.innerHeight || e.clientHeight || g.clientHeight
  return {x, y}
}
export const getMobileBreakPoint = (state) => { return state.mobileBreakPoint }
export const getTooltipColor = (state) => { return state.tooltipColor }



