<template>
<div>
  <v-card class="elevation-10 pa-2 ma-3"
          style="min-height: 87vh;">
    <v-speed-dial v-model="fab"
                  :direction="'right'"
                  absolute
                  :hover="false"
                  :transition="'slide-y-reverse-transition'">
      <v-tooltip bottom
                 slot="activator"
                 :color="getTooltipColor">
        <v-btn slot="activator"
               color="primary"
               dark
               fab
               hover
               v-model="fab">
          <v-icon>settings</v-icon>
          <v-icon>close</v-icon>
        </v-btn>
        <span>Settings</span>
      </v-tooltip>
      <v-tooltip bottom
                 :color="getTooltipColor">
        <v-btn fab
               dark
               slot="activator"
               small
               @click.native.stop="actionDialog = true; setActionValue()"
               class="blue darken-2">
          <v-icon>fa-pencil-square-o</v-icon>
        </v-btn>
        <span>Edit Multiple Nodes</span>
      </v-tooltip>
      <v-tooltip bottom
                 :color="getTooltipColor">
        <v-btn fab
               dark
               small
               slot="activator"
               @click.native.stop="settingDialog = true"
               class="green darken-2">
          <v-icon>important_devices</v-icon>
        </v-btn>
        <span>Change View Settings</span>
      </v-tooltip>
      <v-tooltip bottom
                 :color="getTooltipColor">
        <v-btn fab
               dark
               small
               slot="activator"
               @click.native.stop="setAsyncConfigureNodeDialog(true)"
               class="orange darken-2">
          <v-icon>fa-plus</v-icon>
        </v-btn>
        <span>Add Node</span>
      </v-tooltip>
    </v-speed-dial>
    <br>
    <v-card-title class="border">
      <v-spacer></v-spacer>
      <v-text-field class="border"
                    append-icon="search"
                    label="Search"
                    hide-details
                    v-model="search"></v-text-field>
    </v-card-title>
    <div class="px-2">
      <v-data-table v-model="selected"
                    :headers="headers"
                    :items="matchRegExpr"
                    select-all
                    :loading="isLoading"
                    v-bind:pagination.sync="pagination"
                    selected-key="id"
                    class="elevation-1">
        <template slot="headers"
                  slot-scope="props">
                <tr>
                  <th>
                    <v-checkbox
                      primary
                      hide-details
                      @click.native="toggleAll"
                      :input-value="props.all"
                      :indeterminate="props.indeterminate"
                    ></v-checkbox>
                  </th>
                  <th v-for="(header, i) in props.headers" :key="i"
                    :class="['column sortable', pagination.descending ? 'desc' : 'asc', header.value === pagination.sortBy ? 'active' : '']"
                    @click="changeSort(header.value)" v-if="arrayContains(showControl, header.text)">
                    <v-icon>arrow_upward</v-icon>
                    <span v-if="header.text == 'id'">DeviceID</span>
                    <span v-else>{{ header.text | capitalize }}</span>
                  </th>
                </tr>
              </template>
        <template slot="items"
                  slot-scope="props">
                <tr :active="props.selected" @click="props.selected = !props.selected">
                  <td>
                    <v-checkbox
                      primary
                      hide-details
                      :input-value="props.selected"
                      ></v-checkbox>
                  </td>
                  <td class="text-xs-center" v-for="(value, key) in props.item" nowrap v-if="arrayContains(showControl, key)">
                      {{ value }}
                  </td>
                </tr>
              </template>
      </v-data-table>
    </div>
    <!-- {{checkFormValid()}} -->
    <!-- {{showControl}} -->
    <!-- {{selected}} -->
    <!-- {{actionValue}} -->
    <!-- {{this.getToken}} -->
    <!-- {{getUserData.nodes}} -->
    <!-- {{headers}} -->
    <!-- {{matchRegExpr}} -->
  </v-card>
  <v-dialog v-model="settingDialog"
            lazy
            absolute
            max-width="400">
    <!-- <v-btn primary dark icon slot="activator" class="border">
            <v-icon>settings</v-icon>
        </v-btn> -->
    <v-card>
      <v-card-title>
        <div class="headline">Display Filter</div>
      </v-card-title>
      <v-card-text>
        <v-layout row
                  wrap>
          <v-flex xs12
                  md6
                  v-for="(el, i) in headers"
                  :key="i">
            <v-switch :label="el.text"
                      v-model="showControl"
                      color="red darken-3"
                      :value="el.text"
                      hide-details></v-switch>
          </v-flex>
        </v-layout>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn class="green--text darken-1"
               flat="flat"
               @click.native="settingDialog = false">Close</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
  <v-dialog v-model="actionDialog"
            lazy
            absolute
            max-width="400"
            scrollable>
    <!-- <v-btn primary dark icon slot="activator" @click.native="setActionValue" class="border">
            <v-icon>important_devices</v-icon>
        </v-btn> -->
    <v-card>
      <v-card-title>
        <div class="headline">Actions</div>
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text style="max-height: 500px; overflow: hidden; overflow-y: scroll;">
        <v-layout row
                  wrap>
          <v-flex xs12
                  md6
                  class="px-1">
            <v-text-field name="Name"
                          label="Name"
                          v-model="actionValue.name"
                          :disabled="(selected.length > 1 || selected.length === 0)"
                          :rules="parseRules(actionValidation.name)"></v-text-field>
          </v-flex>
          <v-flex xs12
                  md6
                  class="px-1">
            <v-text-field name="Group Name"
                          label="Group Name"
                          v-model="actionValue.groupName"
                          :disabled="(selected.length === 0)"
                          :rules="parseRules(actionValidation.groupName)"></v-text-field>
          </v-flex>
          <v-flex xs12
                  md6
                  class="px-1">
            <v-text-field name="Min Humidity"
                          label="Min Humidity"
                          v-model="actionValue.humidityMin"
                          suffix="% "
                          :disabled="(selected.length === 0)"
                          :rules="parseRules(actionValidation.humidityMin)"></v-text-field>
          </v-flex>
          <v-flex xs12
                  md6
                  class="px-1">
            <v-text-field name="Max Humidity"
                          label="Max Humidity"
                          v-model="actionValue.humidityMax"
                          suffix="% "
                          :disabled="(selected.length === 0)"
                          :rules="parseRules(actionValidation.humidityMax)"></v-text-field>
          </v-flex>
          <v-flex xs12
                  md6
                  class="px-1">
            <v-text-field name="Min Light"
                          label="Min Light"
                          v-model="actionValue.sunlightMin"
                          suffix="% "
                          :disabled="(selected.length === 0)"
                          :rules="parseRules(actionValidation.sunlightMin)"></v-text-field>
          </v-flex>
          <v-flex xs12
                  md6
                  class="px-1">
            <v-text-field name="Max Light"
                          label="Max Light"
                          v-model="actionValue.sunlightMax"
                          suffix="% "
                          :disabled="(selected.length === 0)"
                          :rules="parseRules(actionValidation.sunlightMax)"></v-text-field>
          </v-flex>
          <v-flex xs12
                  md6
                  class="px-1">
            <v-text-field name="Min Temperature"
                          label="Min Temperature"
                          v-model="actionValue.tempMin"
                          suffix="°C"
                          :disabled="(selected.length === 0)"
                          :rules="parseRules(actionValidation.tempMin)"></v-text-field>
          </v-flex>
          <v-flex xs12
                  md6
                  class="px-1">
            <v-text-field name="Max Temperature"
                          label="Max Temperature"
                          v-model="actionValue.tempMax"
                          suffix="°C"
                          :disabled="(selected.length === 0)"
                          :rules="parseRules(actionValidation.tempMax)"></v-text-field>
          </v-flex>
          <v-flex xs12
                  md6
                  class="px-1">
            <v-text-field name="Min Moisture"
                          label="Min Moisture"
                          v-model="actionValue.moistureMin"
                          suffix="% "
                          :disabled="(selected.length === 0)"
                          :rules="parseRules(actionValidation.moistureMin)"></v-text-field>
          </v-flex>
          <v-flex xs12
                  md6
                  class="px-1">
            <v-text-field name="Max Moisture"
                          label="Max Moisture"
                          v-model="actionValue.moistureMax"
                          suffix="% "
                          :disabled="(selected.length === 0)"
                          :rules="parseRules(actionValidation.moistureMax)"></v-text-field>
          </v-flex>
        </v-layout>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <span>
          <v-btn v-if="checkFormValid() && selected.length !== 0"
                 class="blue--text darken-1"
                 flat="flat"
                 @click.native="actionDialog = false; updateSelectedNodesInfo()">Send</v-btn>
          <v-btn v-else
                 disabled
                 class="blue--text darken-1"
                 flat="flat">Send</v-btn>
        </span>
        <v-btn class="green--text darken-1"
               flat="flat"
               @click.native="actionDialog = false">Close</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</div>
</template>

<script>
import {
  mapGetters,
  mapActions
} from 'vuex'

export default {
  name: 'Configuration',
  data() {
    return {
      firstSetHeader: true,
      fab: true,
      search: '',
      pagination: { sortBy: 'groupName' },
      selected: [],
      multiNodesText: 'Multiple Nodes',
      disableText: '---',
      showControl: ['id', 'name', 'groupName'],
      settingDialog: false,
      actionDialog: false,
      headers: [
        { text: 'Header', align: 'left', value: 'name' }
      ],
      items: [],
      actionValue: {
        'name': '',
        'groupName': '',
        'tempMin': 0,
        'tempMax': 0,
        'humidityMin': 0,
        'humidityMax': 0,
        'moistureMin': 0,
        'moistureMax': 0,
        'sunlightMin': 0,
        'sunlightMax': 0
      },
      actionValidation: {
        'name': ['required'],
        'groupName': ['required'],
        'tempMin': ['required', 'temperature', 'isInRange', 'isValidInt'],
        'tempMax': ['required', 'temperature', 'isInRange', 'isValidInt'],
        'humidityMin': ['required', 'humidity', 'isInRange', 'isValidInt'],
        'humidityMax': ['required', 'humidity', 'isInRange', 'isValidInt'],
        'moistureMin': ['required', 'moisture', 'isInRange', 'isValidInt'],
        'moistureMax': ['required', 'moisture', 'isInRange', 'isValidInt'],
        'sunlightMin': ['required', 'sunlight', 'isInRange', 'isValidInt'],
        'sunlightMax': ['required', 'sunlight', 'isInRange', 'isValidInt']
      },
      isFormValid: true,
      rules: {
        required: (value) => {
          if (value === '') return 'Required.'
          else return true
        },
        humidity: () => { return this.minMaxCheck(this.actionValue.humidityMin, this.actionValue.humidityMax) },
        sunlight: () => { return this.minMaxCheck(this.actionValue.sunlightMin, this.actionValue.sunlightMax) },
        moisture: () => { return this.minMaxCheck(this.actionValue.moistureMin, this.actionValue.moistureMax) },
        temperature: () => { return this.minMaxCheck(this.actionValue.tempMin, this.actionValue.tempMax) },
        isInRange: (val) => {
          if ((parseInt(val) < 0) || (parseInt(val) > 100)) return 'Value not in range.'
          else return true
        },
        isValidInt: (val) => {
          if (!this.isNumber(val) && val !== this.multiNodesText && val !== this.disableText) return 'Value needs to be an Integer.'
          else return true
        }
      }
    }
  },
  created() {
    // console.log(this._.random(20))
    // this.setHeader()
  },
  mounted() {
    this.setHeader()
  },
  watch: {
    getUserData: {
      handler: function () {
        // console.log('getUserDataChange')
        if (this.firstSetHeader) {
          this.firstSetHeader = false
          this.setHeader()
        }
      },
      deep: true
    }
  },
  computed: {
    ...mapGetters(['getTooltipColor']),
    ...mapGetters('user', ['getUserData', 'getToken']),
    isLoading() {
      if (!this.getUserData) return true
      else return false
    },
    matchRegExpr() {
      if (!this.getUserData) return []
      const re = new RegExp(this.search, 'i')
      const list = this.getUserData.nodes.filter((node) => {
        for (let key of Object.keys(node)) { if (node[key]) { if (node[key].toString().match(re) && this.arrayContains(this.showControl, key)) { return true } } }
        return false
      })
      return list
    }
  },
  methods: {
    ...mapActions(['setAsyncConfigureNodeDialog']),
    ...mapActions('user', [
      'setAsyncUserData'
    ]),
    ...mapActions('user/node', [
      'updateAsyncNodesInfo'
    ]),
    minMaxCheck(min, max) {
      if (parseInt(min) > parseInt(max)) return 'Min > Max.'
      else return true
    },
    isNumber(val) {
      const re = new RegExp(/\d*/)
      /* eslint-disable eqeqeq */
      if (parseInt(String(val).match(re)[0]) != val) return false
      else return true
    },
    parseRules(list) {
      let rules = []
      for (let r of list) { rules.push(this.rules[r]) }
      return rules
    },
    setHeader() {
      this.headers = []
      if (!this.getUserData) return
      if (this.getUserData.nodes.length === 0) return
      const list = Object.keys(this.getUserData.nodes[0])
        .map(o => { return { text: o, value: o } })
      for (let li of list) { if (li.text !== 'active') this.headers.push(li) }
    },
    setActionValue() {
      if (this.selected.length > 1) {
        for (let key of Object.keys(this.actionValue)) { this.actionValue[key] = this.multiNodesText } // Change Later
        this.actionValue.name = this.multiNodesText
      } else if (this.selected.length === 0) {
        for (let key of Object.keys(this.actionValue)) { this.actionValue[key] = this.disableText }
        this.actionValue.name = 'No Node Selected'
      } else { // === 1
        for (let key of Object.keys(this.actionValue)) { this.actionValue[key] = this.selected[0][key] }
      }
    },
    toggleAll() {
      if (this.selected.length) this.selected = []
      else this.selected = this.matchRegExpr.slice()
    },
    changeSort(column) {
      if (this.pagination.sortBy === column) {
        this.pagination.descending = !this.pagination.descending
      } else {
        this.pagination.sortBy = column
        this.pagination.descending = false
      }
    },
    arrayContains(list, el) {
      for (let l of list) { if (JSON.stringify(l) === JSON.stringify(el)) { return true } }
      return false
    },
    updateSelectedNodesInfo() {
      let nodes = []
      for (let node of this.selected) { nodes.push(node.id) }
      this.updateAsyncNodesInfo({ nodes, value: this.getParseActionValue() })
    },
    getParseActionValue() { // urlencoded and what to send logic
      let toSend = {}
      for (let key of Object.keys(this.actionValue)) { if (this.actionValue[key] !== this.multiNodesText) toSend[key] = this.actionValue[key] }
      return toSend
    },
    checkFormValid() {
      for (let key of Object.keys(this.actionValue)) {
        for (let ruleName of this.actionValidation[key]) { if (this.rules[ruleName](this.actionValue[key]) !== true) return false }
      }
      return true
    }
  }
}
</script>

