<!-- =========================== -->
<!-- 9/17/2017 Template by Ewing -->
<!-- =========================== -->

<template>
<v-card class="elevation-5 mb-2">
  <v-card-title class="subheading py-1 pl-3 pr-1">
    Recent Notifications
    <v-spacer></v-spacer>
    <v-tooltip top
               :color="getTooltipColor">
      <v-btn icon
             slot="activator"
             @click="expand = !expand"
             class="pa-0 ma-0">
        <v-icon v-if="expand">expand_less</v-icon>
        <v-icon v-else>expand_more</v-icon>
      </v-btn>
      <span v-if="!expand">Expand</span>
      <span v-else>Hide</span>
    </v-tooltip>
  </v-card-title>
  <v-divider></v-divider>
  <transition name="expand-lg">
    <v-card-text class="pa-0"
                 v-if="expand"
                 style="overflow: hidden">
      <!-- <transition name="expand-xs">
        <v-progress-linear :indeterminate="true"
                           v-if="getNotificationsByNode === null"
                           color="indigo darken-4"
                           background-color="indigo lighten-4"
                           class="pa-0 ma-0"></v-progress-linear>
      </transition> -->
      <v-list class="pl-2">
        <transition-group name="group-expand"
                          mode="out-in">
          <v-list-tile avatar
                       v-for="noti in recentNotification"
                       :key="noti.id">
            <v-list-tile-action class="border">
              <SensorAvatar :sensor="noti.sensor"></SensorAvatar>
            </v-list-tile-action>
            <v-list-tile-content class="border body-2">
              <v-list-tile-title>
                <v-layout row
                          wrap>
                  <v-flex xs12
                          md12
                          class="border notificationTile">
                    On {{epocToCustomDateString(new Date(noti.createdAt).getTime())}} at {{ epocToCustomTimeString(new Date(noti.createdAt).getTime()) }} {{noti.sensor | capitalize}} was {{noti.reading}}{{getSensorOptions[noti.sensor].unit}} which is {{(noti.overMax ? 'above' : 'below')}} your defined ideal range.
                  </v-flex>
                </v-layout>
              </v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-avatar class="border">
              <v-tooltip left
                         :color="getTooltipColor">
                <v-btn icon
                       slot="activator">
                  <v-icon>chat</v-icon>
                </v-btn>
                <span>{{`${noti.sensor} sensor ${(noti.overMax ? '+' : '-')}${Math.abs(parseInt(noti.reading) - parseInt(selectedNode[dbSensorName(noti.sensor)+(noti.overMax ? 'Max' : 'Min')]))}`}}</span>
              </v-tooltip>
            </v-list-tile-avatar>
          </v-list-tile>
        </transition-group>
      </v-list>
    </v-card-text>
  </transition>
</v-card>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import { dbSensorName } from '@/js/transforms.js'
import SensorAvatar from '@/components/SensorAvatar.vue'

// import { server } from '@/js/axios.js'

export default {
  name: 'RecentNotificationBox',
  components: { SensorAvatar },
  props: { nodeId: { type: String, required: true } },
  data() {
    return {
      recentNotification: [],
      expand: true
    }
  },
  created() {},
  mounted() {},
  beforeDestory() {},
  watch: {
    getNotificationsByNode() {
      if (!this.getNotificationsByNode || !this.selectedNode) this.recentNotification = []
      else if (!this.getNotificationsByNode[this.selectedNode.id]) this.recentNotification = []
      else this.recentNotification = this.getNotificationsByNode[this.selectedNode.id].slice(0, 5)
    },
    selectedNode() {
      if (!this.getNotificationsByNode || !this.selectedNode) this.recentNotification = []
      else if (!this.getNotificationsByNode[this.selectedNode.id]) this.recentNotification = []
      else this.recentNotification = this.getNotificationsByNode[this.selectedNode.id].slice(0, 5)
    }
  },
  computed: {
    ...mapGetters(['getSelectedNodeId', 'getTooltipColor']),
    ...mapGetters('user/node', ['getNodeInfoById', 'getSensorOptions']),
    ...mapGetters('user/notification', ['getNotificationsByNode']),
    selectedNode() { return this.getNodeInfoById(this.nodeId) }
  },
  methods: {
    ...mapActions([]),
    dbSensorName: dbSensorName,
    epocToCustomTimeString(epoc) {
      const time = new Date(epoc).toLocaleTimeString()
      const AMPM = time.split(' ')[1]
      const timeWithoutSecond = time.split(':').slice(0, 2).join(':')
      return `${timeWithoutSecond} ${AMPM}`
    },
    epocToCustomDateString(epoc) {
      let options
      options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric', hour: 'numeric', hour12: true }
      const date = new Date(epoc).toLocaleString('en-US', options)
      let stringDate = ''
      let listDate = date.split(', ')
      listDate[1] = listDate[1].split(' ')[0] + ' ' + listDate[1].split(' ')[1] + this.nth(listDate[1].split(' ')[1])
      stringDate = listDate[1]
      return stringDate
    },
    nth(d) {
      if (d > 3 && d < 21) return 'th' // thanks kennebec
      switch (d % 10) {
        case 1:
          return 'st'
        case 2:
          return 'nd'
        case 3:
          return 'rd'
        default:
          return 'th'
      }
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="sass" scoped>
.border
    // border: 1px dashed grey
.notificationTile
    white-space: nowrap
    text-overflow: ellipsis
    width: 100%
    display: block
    overflow: hidden

.expand-xs-enter
  max-height: 0px
.expand-xs-enter-active
  transition: max-height 0.5s ease-in
.expand-xs-enter-to
  max-height: 20px
.expand-xs-leave
  max-height: 20px
.expand-xs-leave-active
  transition: max-height 1s ease-in
.expand-xs-leave-to
  max-height: 0px

.expand-lg-enter
  max-height: 0px
  overflow: hidden
.expand-lg-enter-active
  transition: max-height 1s ease-in-out
.expand-lg-enter-to
  max-height: 600px
.expand-lg-leave
  max-height: 600px
  overflow: hidden
.expand-lg-leave-active
  transition: max-height 1s ease-out
.expand-lg-leave-to
  max-height: 0px

.group-expand-enter
  max-width: 0px
  max-height: 0px
  opacity: 0
.group-expand-enter-active
  transition: max-width 1s 0.8s ease-in-out, max-height 1s ease-in-out, opacity 1s
.group-expand-enter-to
  opacity: 1
  max-height: 500px
  max-width: 100%
.group-expand-leave
  opacity: 1
  max-height: 500px
  max-width: 100%
.group-expand-leave-active
  transition: max-width 0.1s ease-in-out, max-height 0.1s ease-in-out
.group-expand-leave-to
  max-width: 0px
  max-height: 0px
  opacity: 0
</style>



