<script>
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'ConfigureNode',
  data() {
    return {
      step: 1,
      configNodeUrl: (process.env.NODE_ENV === 'production' ? 'http://192.168.4.1' : 'http://127.0.0.1:3000/configureNode.html'),
      wifiList: [],
      getWifiListInterval: '',
      ssid: '',
      passwd: '',
      isConnecting: false,
      connectDotValue: 1,
      connectDotInterval: '',
      connectText: '',
      hasConnected: false,
      showDialog: false,
      fullscreen: false,
      cardHeight: '300px'
    }
  },
  created() {},
  watch: {
    getWelcomeDialog(val) {
      if (val) this.setAsyncDrawer(false)
      this.showDialog = val
    },
    showDialog(val) { this.setAsyncWelcomeDialog(val) },
    fullscreen(val) {
      const sysBar = 32
      const stepper = 87
      const dialogBorder = 80
      this.cardHeight = val ? `${this.getViewport.y - dialogBorder - stepper - sysBar}px` : '300px'
    }
  },
  computed: {
    ...mapGetters(['getCompanyName', 'getWelcomeDialog', 'getViewport']),
    ...mapGetters('user', ['getUserData', 'getToken'])
  },
  methods: {
    ...mapActions([
      'setAsyncActivePage',
      'setAsyncWelcomeDialog',
      'setAsyncConfigureNodeDialog',
      'setAsyncDrawer'
    ]),
    previous() {
      if (this.step < 1) this.step = 1
      else this.step -= 1
    },
    next() { this.step += 1 },
    done() {
      this.setAsyncWelcomeDialog(false)
      this.step = 1
      this.fullscreen = false
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="sass" scoped>
.border
    border: 1px dashed grey
.pointer
    cursor: pointer
.stepper-card
    font-family: 'Andale Mono', 'Arial'
    color: white
    overflow: hidden
    overflow-y: scroll
    .paragraph-1
