import { server } from '@/js/axios.js'
import * as account from '@/js/account'
import { SensorOptions } from '@/style'
import qs from 'qs'

const state = {
  nodeByGroup: null,
  nodeByName: null,
  allSensors: [],
  rawNodeData: null,
  nodeData: {},
  allNodeId: [],
  latestReadings: null,
  sensorOptions: SensorOptions
}

const getters = {
  getAllSensors (state) { return state.allSensors },
  // getNodeByGroup (state) { return state.nodeByGroup },
  getNodeByGroup (state) { return state.nodeByGroup }, // Object
  getNodeByName (state) { return state.nodeByName }, // Array
  getNodeById (state) { return state.nodeByName }, // Array
  getAllNodeId (state, getters, rootState) { return state.allNodeId },
  getNodeInfoById (state, getters, rootState) {
    return (id) => {
      if (rootState.user.userData) {
        for (let node of rootState.user.userData.nodes) { if (node.id === parseInt(id)) return node }
        return { name: 'no_node_found' }
      } else return { name: 'no_node_found' }
    }
  },
  getSensorOptions (state) { return state.sensorOptions },
  getAllNodeData (state) { return state.nodeData },
  getLatestReadings (state) { return state.latestReadings },
  getRawNodeData (state) { return state.rawNodeData }
}

const mutations = {
  parseNodeByGroup (state, payload) {
    if (process.env.NODE_ENV !== 'production') console.log('parseNodeByGroup')
    state.nodeByGroup = {}
    for (let n of payload) {
      if (!(n.groupName in state.nodeByGroup)) state.nodeByGroup[n.groupName] = [n]
      else state.nodeByGroup[n.groupName].unshift(n)
    }
  },
  parseNodeByName (state, payload) {
    if (process.env.NODE_ENV !== 'production') console.log('parseNodeByName')
    payload.sort((a, b) => {
      const max = Math.max(a.name.length, b.name.length)
      for (let i = 0; i < max; i++) {
        if (!a.name.charCodeAt(i)) return 1
        if (!b.name.charCodeAt(i)) return -1
        const val = a.name.charCodeAt(i) - b.name.charCodeAt(i)
        if (val !== 0) return val
      }
      return 0
    })
    state.nodeByName = payload
  },
  parseNodeById (state, payload) {
    if (process.env.NODE_ENV !== 'production') console.log('parseNodeById')
    state.nodeById = payload
  },
  parseSensors (state, payload) {
    if (process.env.NODE_ENV !== 'production') console.log('parseSensors')
    state.allSensors = [
      {value: 'moisture', name: 'moisture'},
      {value: 'sunlight', name: 'light'},
      {value: 'humidity', name: 'humidity'},
      {value: 'temperature', name: 'temperature'}
    ]
  },
  setRawNodeData (state, payload) {
    state.rawNodeData = payload
  },
  setNodeData (state, payload) {
    if (process.env.NODE_ENV !== 'production') console.log('setNodeData in store')
    state.nodeData = Object.assign({}, state.nodeData, {[payload.id]: payload})
  },
  setAllNodeId (state, payload) {
    if (process.env.NODE_ENV !== 'production') console.log('setAllNodeId in store')
    let list = []
    for (let n of payload) { list.push(n.id) }
    state.allNodeId = list
  },
  setLatestReadings (state, payload) {
    state.latestReadings = Object.assign({}, state.latestReadings, payload)
  }
}

const actions = {
  setAsyncUpdatedNodeInfo ({ state, commit, rootState, dispatch }, payload) {
    commit('user/setUpdatedNodeInfo', payload, {root: true})
    dispatch('setAsyncParseAllNode')
  },
  setAsyncParseAllNode({ state, commit, rootState }) {
    const nodes = rootState.user.userData.nodes
    commit('setAllNodeId', nodes)
    commit('parseNodeByGroup', nodes)
    commit('parseNodeByName', nodes)
    commit('parseSensors', nodes)
    commit('parseNodeById', nodes)
  },
  setAsyncNodeData ({ state, commit, rootState }, payload) {
    if (process.env.NODE_ENV !== 'production') console.log('setAsyncNodeData')
    // let userHasNode = false
    // if (rootState.user.userData) for (let n of rootState.user.userData.nodes) { if (payload.id === n.id) userHasNode = true }
    // if (!userHasNode) return
    server.get(`/nodes/prev_xh/${payload.id}?hours=${payload.hour}`)
        .then((response) => {
          commit('setRawNodeData', response) // No use might delete in the future
          const nodeData = { id: payload.id, moisture: [], sunlight: [], temperature: [], humidity: [], time: [] }
          for (let data of response.data) {
            const time = new Date(data.createdAt).getTime()
            nodeData.moisture.push({ y: data.moisture, x: time })
            nodeData.sunlight.push({ y: data.sunlight, x: time })
            nodeData.temperature.push({ y: data.temperature, x: time })
            nodeData.humidity.push({ y: data.humidity, x: time })
          }
          commit('setNodeData', nodeData)
        })
        .catch((err) => {
          if (process.env.NODE_ENV !== 'production') console.log(err.response)
          account.errorHandler(err)
          if (err.response.status === 404) {
            commit('setNodeData', { id: payload.id, moisture: [], sunlight: [], temperature: [], humidity: [], time: [] })
          }
        })
  },
  setAsyncLatestReading ({ state, commit, rootState }, payload) {
    if (process.env.NODE_ENV !== 'production') console.log('setAsyncLatestReading')
    server.get(`/nodes/status?nodes=${payload.join(',')}`)
          .then((response) => {
            // if (response.data.length !== 0)
            commit('setLatestReadings', response.data)
            // else commit('setLatestReadings', { nodeId: payload })
          })
          .catch((err) => {
            if (process.env.NODE_ENV !== 'production') console.log(err.response)
            account.errorHandler(err)
          })
  },
  updateAsyncNodesInfo({ state, commit, rootState }, payload) { // Takes in an array of node id
    if (process.env.NODE_ENV !== 'production') console.log('updateNodesInfo')
    server.put(`/nodes/update?nodes=${payload.nodes.join(',')}`, qs.stringify(payload.value))
      .then((res) => {
        commit('user/setUpdatedNodeInfo', res.data, {root: true})
        commit('setAllNodeId', rootState.user.userData.nodes)
        commit('parseNodeByGroup', rootState.user.userData.nodes)
        commit('parseSensors', rootState.user.userData.nodes)
      })
      .catch((err) => {
        if (process.env.NODE_ENV !== 'production') console.log(err.response)
        account.errorHandler(err)
      })
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}



