<!-- 9/17/2017 Template by Ewing -->
<!-- =========================== -->

<template>
<v-tooltip right
           :color="getTooltipColor">
  <span slot="activator">
    <span v-if="getLatestReadings">
      <span v-if="latestReading && Object.keys(latestReading).length > 1">
        <!-- {{latestReading.battery}} -->
        <span v-if="latestReading.battery >= 80">
          <v-icon color="green"
                  style="font-size: 15px">fa-battery-full</v-icon>
        </span>
        <span v-else-if="latestReading.battery >= 50">
          <v-icon color="yellow"
                  style="font-size: 15px">fa-battery-three-quarters</v-icon>
        </span>
        <span v-else-if="latestReading.battery >= 30">
          <v-icon color="orange"
                  style="font-size: 15px">fa-battery-half</v-icon>
        </span>
        <span v-else-if="latestReading.battery >= 10">
          <v-icon color="red"
                  style="font-size: 15px">fa-battery-quarter</v-icon>
        </span>
        <span v-else-if="latestReading.battery >= 0 && latestReading.battery !== null">
          <v-icon color="red"
                  style="font-size: 15px">fa-battery-empty</v-icon>
        </span>
        <span v-else><b>---</b>&nbsp;</span>
      </span>
      <span v-else>
        <v-icon color="red">fa-exclamation-circle</v-icon>
      </span>
    </span>
    <span v-else>
      <!-- <v-icon color="red">fa-exclamation-circle</v-icon> -->
      <v-progress-circular indeterminate
                           v-bind:size="14"
                           v-bind:width="7"
                           color="teal"></v-progress-circular>
    </span>
  </span>
  <span>
    <span v-if="getLatestReadings">
      <span v-if="latestReading && Object.keys(latestReading).length > 1">
        <span v-if="latestReading.battery || latestReading.battery === 0"
              style="font-size: 16px">
          <b>{{latestReading.battery || 0}}%</b>
        </span>
        <span v-else>
          No Battery Info
        </span>
      </span>
      <span v-else>
        Device Inactive <br> Please Restart Device
      </span>
    </span>
  </span>
</v-tooltip>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
// import { server } from '@/js/axios.js'

export default {
  name: 'DeviceBattery',
  components: {},
  props: {
    nodeID: {
      type: String,
      required: true
    }
  },
  data() {
    return {}
  },
  created() {},
  mounted() {},
  beforeDestory() {},
  watch: {},
  computed: {
    ...mapGetters(['getTooltipColor']),
    ...mapGetters('user/node', ['getLatestReadings']),
    latestReading() {
      return this.getLatestReadings[this.nodeID].latest
    }
  },
  methods: {
    ...mapActions([]),
    test() {}
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="sass" scoped>
.border
    border: 1px dashed grey
</style>



