<!-- =========================== -->
<!-- 9/17/2017 Template by Ewing -->
<!-- =========================== -->
<template>
<v-tooltip :left="getDrawer"
           :right="!getDrawer"
           :color="getTooltipColor">
  <!-- :class="`${getSensorOptions[sensor].colorName} lighten-4`" -->
  <v-avatar slot="activator"
            :size="size">
    <img v-if="sensor === 'humidity'"
         src="~@/assets/humidity.png" />
    <img v-else-if="sensor === 'moisture'"
         src="~@/assets/moisture.png" />
    <img v-else-if="sensor === 'sunlight'"
         src="~@/assets/sunlight.png" />
    <img v-else-if="sensor === 'temperature'"
         src="~@/assets/temperature.png" />
    <img v-else
         src="~@/assets/logo.png" />
  </v-avatar>
  <span>{{sensor | capitalize}}</span>
</v-tooltip>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'SensorAvatar',
  components: {},
  props: {
    sensor: { type: String, default: 'Error' },
    size: { type: String, default: '35px' }
  },
  data() { return {} },
  created() {},
  mounted() {},
  beforeDestory() {},
  watch: {},
  computed: {
    ...mapGetters(['getDrawer', 'getTooltipColor']),
    ...mapGetters('user/node', ['getSensorOptions'])
  },
  methods: {}
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="sass" scoped>
.border
    border: 1px dashed grey
</style>



