export const dbSensorName = (sensor) => {
  if (sensor === 'temperature') return 'temp'
  else return sensor
}

export const emptyNodeNameString = (nodeId) => `Node ID: ${nodeId}`

export const capitalize = (str) => {
  if (!str && str !== 0) return ''
  str = str.toString()
  return str.charAt(0).toUpperCase() + str.slice(1)
}



