/* eslint-disable */

export const newChart = function (elementId, timeWindow) {
	if (timeWindow < 0 || timeWindow > 3) {
		return;
	}

	var TIME_WINDOW_DAY = 0;
	var TIME_WINDOW_WEEK = 1;
	var TIME_WINDOW_MONTH = 2;
	var TIME_WINDOW_YEAR = 3;
	var HOUR = 60 * 60 * 1000;
	var DAY = 24 * HOUR;

	var now = new Date();
	var to, delta, xDelta;
	switch(timeWindow) {
		case TIME_WINDOW_DAY: // Last 24 hours
			to = new Date(Math.ceil(now.getTime()/HOUR)*HOUR);
			xDelta = HOUR;
			delta = xDelta * 25;
			break;
		case TIME_WINDOW_WEEK: // Last week
			to = new Date(now);
			to = new Date(to.setHours(23,59,59,999) + 1);
			xDelta = DAY;
			delta = xDelta * 8;
			break;
		case TIME_WINDOW_MONTH: // Last month
			to = new Date(now);
			to = new Date(to.setHours(23,59,59,999) + 1);
			xDelta = DAY * 7;
			delta = xDelta * 5;
			break;
		case TIME_WINDOW_YEAR: // Last year

			break;
		default:
			return;
			break;
	}

	var xFrom = new Date(to.getTime() -
		delta).getTime();
	var xTo = to.getTime();
	var xLines = (xTo - xFrom) / xDelta;

	// Set some constants
	var X_EDGE = 50;
	var Y_EDGE = 30;
	var START_X = 0;
	var FONT_SIZE = 12;
	var TEXT_PAD = 10;
	var TEXT_X = X_EDGE - 10;

	// Not REALLY constants
	var END_WIDTH = 0;
	var END_HEIGHT = 0;
	var TEXT_Y = 0;


	var chart = {
		onLoad: function() {
			this.canvas = document.getElementById(elementId);
			this.ctx = this.canvas.getContext('2d');

			// Set canvas dimensions
			var canvOffset =
				chart.canvas.getBoundingClientRect();
			this.canvas.width = canvOffset.width;
			this.canvas.height = canvOffset.height;

			// Calculate scales
			END_WIDTH = this.canvas.width - X_EDGE;
			END_HEIGHT = this.canvas.height - Y_EDGE;
			TEXT_Y = END_HEIGHT + 20;
			this.xScale = (END_WIDTH - X_EDGE - START_X*2) / (xTo - xFrom);
			//this.yScale = (END_HEIGHT - Y_EDGE) /
			//	(this.yTo[this.mainGraph] - this.yFrom[this.mainGraph]);

			// Give canvas events
			this.canvas.addEventListener("pointerdown",
				this.onPointerDownRef, false);
			this.canvas.addEventListener("pointermove",
				this.onPointerMoveRef, false);
			this.canvas.addEventListener("pointerup",
				this.onPointerUpRef, false);

			// Set time window
			this.getTimeString =
				(timeWindow == TIME_WINDOW_DAY)? this.getTimeStringHour:
				(timeWindow == TIME_WINDOW_WEEK)? this.getTimeStringDay:
				(timeWindow == TIME_WINDOW_MONTH)? this.getTimeStringWeek:
				this.getTimeStringMonth;

			// Various other data
			this.pointersDown = false;
			this.graphs = {};
			this.mainGraph = null;
			this.highlighted = null;
			this.removeHighlight = false;
			this.mousePosition = null;
			this.showBadRegions = true;
			this.badBounds = {};
			this.graphColors = {};
			this.showGraphs = {};
			this.badColors = {};
			this.yFrom = {};
			this.yTo = {};
			this.yDelta = {};
			this.yLabel = {};
			this.yLines = {};

			window.addEventListener('resize', function() {
				chart.onResize();
			});

			this.redrawChart();
		},

		onResize: function() {
			var canvOffset =
				chart.canvas.getBoundingClientRect();
			this.canvas.width = canvOffset.width;
			this.canvas.height = canvOffset.height;

			END_WIDTH = this.canvas.width - X_EDGE;
			END_HEIGHT = this.canvas.height - Y_EDGE;
			TEXT_Y = END_HEIGHT + 20;

			// Calculate scales
			this.xScale = (END_WIDTH - X_EDGE - START_X*2) / (xTo - xFrom);
			this.yScale = (END_HEIGHT - Y_EDGE) /
				(this.yTo[this.mainGraph] - this.yFrom[this.mainGraph]);

			this.redrawChart();
		},

		/**
			Draw methods
		*/

		redrawChart: function() {
			// Clear the grid
			this.ctx.clearRect(0, 0, this.canvas.width,
				this.canvas.height);

			// Draw the grid
			this.ctx.font = FONT_SIZE + 'px Arial';
			var xDiff = (this.canvas.width -
				(X_EDGE + START_X) * 2) / (xLines);
			var yDiff = (this.canvas.height -
				Y_EDGE * 2) / (this.yLines[this.mainGraph]);

			var showText = 0;

			for (var i = 0; i <= this.yLines[this.mainGraph]; ++i) {
				var temp = Y_EDGE + (yDiff * i);
				this.drawLine(X_EDGE, temp, END_WIDTH, temp,
					2, '#eaebec');

				// Draw label
				if (i > 0 && i < this.yLines[this.mainGraph]) {
					var label = this.yTo[this.mainGraph] - this.yDelta[this.mainGraph] * i;
					this.drawText(TEXT_X, temp + FONT_SIZE/3,
						label + this.yLabel[this.mainGraph], 'end', '#b2b4b8');
				}
			}
			for (var i = 0; i <= xLines; ++i) {
				var temp = X_EDGE + START_X + (xDiff * i);
				this.drawLine(temp, Y_EDGE, temp, END_HEIGHT,
					2, '#eaebec');

				if (xDiff > 40 || showText == 0 || (i == xLines && timeWindow != 0)) {
					this.drawCircle(temp, Y_EDGE, 4, '#88d7e4', true);
					var label = this.getTimeString(xFrom + xDelta * i);
					this.drawText(temp, TEXT_Y,
						label,	'center', '#b2b4b8');
				}
				if (timeWindow == 0)
					if (xDiff > 20) {
						showText = (++showText) % 2;
					} else if (xDiff > 7) {
						showText = (++showText) % 5;
					} else {
						showText = (++showText) % 12;
					}
				else {
					showText = (++showText) % 3;
				}
			}


			// Draw graphs
			var graphs = Object.keys(this.graphs);
			for (var i = 0; i < graphs.length; ++i) {
				if (this.showGraphs[graphs[i]]) {
					this.drawGraph(graphs[i]);
				}
			}

			// Draw bottom line of grid
			this.drawLine(X_EDGE, END_HEIGHT, END_WIDTH, END_HEIGHT,
				2, '#c2c4c8');

			if (this.highlighted != null && this.mainGraph != null) {
				var graph = this.graphs[this.mainGraph];
				var point = graph[this.highlighted];
				var x = this.toChartX(point.x);
				var y = this.toChartY(point.y);

				this.drawLine(x, Y_EDGE, x, END_HEIGHT, 1, "#000000");
				this.drawCircle(x, y, 5, '#88d7e4', true);
				this.drawCircle(x, y, 5, '#000000', false);

				var cleanX = this.getDateTime(point.x);
				var cleanY = Math.round(point.y * 100) / 100;

				this.drawInfoBox(this.mousePosition.x,
					this.mousePosition.y, cleanX,
					'Value: ' + cleanY + this.yLabel[this.mainGraph], '#88d7e4');
			}
		},

		drawGraph: function(graphName) {
			var graph = this.graphs[graphName];

			var grad1 = this.ctx.createLinearGradient(
				X_EDGE + START_X, 0, END_WIDTH, 0);
			var grad2 = this.ctx.createLinearGradient(
				X_EDGE + START_X, 0, END_WIDTH, 0);

			if (graph.length == 0) return;
			if (graph.length == 1) {
				var x = this.toChartX(graph[0].x);
				var y = this.toChartY(graph[0].y);
				this.drawLine(x,y, x, END_HEIGHT,
					5, this.graphColors[graphName][0]);
				return;
			}

			var startDrawing = false;
			var startX = this.toChartX(graph[0].x);
			var startY = this.toChartY(graph[0].y);
			var oldX = startX;
			var oldY = startY;

			for (var i = 1; i < graph.length; ++i) {
				var thisX = this.toChartX(graph[i].x);
				var thisY = this.toChartY(graph[i].y);

				var grdX = (thisX - (X_EDGE + START_X)) / (END_WIDTH - X_EDGE);

				if (grdX < 0 || grdX > 1) {
					oldX = thisX;
					oldY = thisY;
					continue;
				}
				if (!startDrawing) {
					this.ctx.beginPath();
					this.ctx.moveTo(thisX,thisY);
					startX = thisX;
					startY = thisY;
					oldX = thisX;
					oldY = thisY;
					startDrawing = true;
				}
				if (graph[i].y < this.badBounds[graphName][0] ||
					graph[i].y > this.badBounds[graphName][1]) {
					grad1.addColorStop(grdX, this.badColors[graphName][1]);
					grad2.addColorStop(grdX, this.badColors[graphName][0]);
				} else {
					grad1.addColorStop(grdX, this.graphColors[graphName][1]);
					grad2.addColorStop(grdX, this.graphColors[graphName][0]);
				}
				this.bezierCurveFromTo(oldX, oldY, thisX, thisY);
				oldX = thisX;
				oldY = thisY;
			}

			if (this.showBadRegions) {
				this.ctx.fillStyle = grad1;
				this.ctx.strokeStyle = grad2;
			} else {
				this.ctx.fillStyle = this.graphColors[graphName][1];
				this.ctx.strokeStyle = this.graphColors[graphName][0];
			}

			this.ctx.stroke();

			this.ctx.lineTo(oldX, END_HEIGHT);
			this.ctx.lineTo(startX, END_HEIGHT);

			this.ctx.closePath();
			this.ctx.fill();
		},

		drawInfoBox: function(x, y, txt1, txt2, col) {
			var max = Math.max(txt2.length, txt1.length)
			var width = 7 * max;
			var height = 40;
			var edge = 25;
			var padding = 10;
			var drawX = x +
				((x + width >= END_WIDTH - edge)?
				-(padding + width): padding);
			var drawY = y +
				((y + height >= END_HEIGHT - edge)?
				-(padding + height): padding);
			this.drawRect(drawX, drawY, width, height,
				5, 'rgba(75,75,75,0.9)', true);
			this.ctx.font = '12px Arial';
			this.drawText(drawX + 5, drawY + 16,
				txt1, 'left', '#ffffff');
			this.drawCircle(drawX + 9, drawY + 28, 4,
				col, true);
			this.drawText(drawX + 16, drawY + 32,
				txt2, 'left', '#ffffff');
		},

		bezierCurveFromTo: function(x1, y1, x2, y2) {
			var FACTOR = 2;
			var cp1x = x1 + (Math.abs(x2 - x1) / FACTOR);
			var cp1y = y1;
			var cp2x = x2 - (Math.abs(x2 - x1) / FACTOR);
			var cp2y = y2;
			this.ctx.bezierCurveTo(cp1x, cp1y, cp2x, cp2y, x2, y2);
		},

		drawLine: function(x1, y1, x2, y2, width, col) {
			// Create a line from x1,y1 to x2,y2 of
			// width width and color col.
			this.ctx.strokeStyle = col;
			this.ctx.lineWidth = width;
			this.ctx.beginPath();
			this.ctx.moveTo(x1, y1);
			this.ctx.lineTo(x2, y2);
			this.ctx.closePath();
			this.ctx.stroke();
		},

		drawCircle: function(x, y, rad, col, filled) {
			// Create a circle at x,y of
			// radius rad and color col.
			this.ctx.strokeStyle = col;
			this.ctx.fillStyle = col;
			this.ctx.beginPath();
			this.ctx.arc(x, y, rad, 0, 2 * Math.PI, false);
			this.ctx.closePath();
			if (filled) {
				this.ctx.fill();
			} else {
				this.ctx.stroke();
			}
		},

		drawRect: function(x, y, width, height, rad, col, filled) {
			if (typeof rad === 'number') {
				rad = {tl: rad, tr: rad, br: rad, bl: rad};
			}
			this.ctx.strokeStyle = col;
			this.ctx.fillStyle = col;
			this.ctx.beginPath();
			this.ctx.moveTo(x + rad.tl, y);
			this.ctx.lineTo(x + width - rad.tr, y);
			this.ctx.arcTo(x + width, y, x + width,
				y + rad.tr, rad.tr);
			this.ctx.lineTo(x + width, y + height - rad.br);
			this.ctx.arcTo(x + width, y + height,
				x + width - rad.br, y + height, rad.br);
			this.ctx.lineTo(x + rad.bl, y + height);
			this.ctx.arcTo(x, y + height,
				x, y + height - rad.bl, rad.bl);
			this.ctx.lineTo(x, y + rad.tl);
			this.ctx.arcTo(x, y, x + rad.tl, y, rad.tl);

			this.ctx.closePath();
			if (filled) {
				this.ctx.fill();
			} else {
				this.ctx.stroke();
			}
		},

		drawText: function(x, y, txt, align, col) {
			this.ctx.fillStyle = col;
			this.ctx.textAlign = align;
			this.ctx.fillText(txt, x, y);
		},

		/**
			Utility Methods
		*/

		binarySearch: function(graphName, xVal) {
			var graph = this.graphs[graphName];

			if (graph.length == 0)
				return 0;

			return this.binarySearchUtil(graph,
				xVal, 0, graph.length - 1);
		},

		binarySearchUtil: function(graph, xVal, low, high) {
			if (high <= low)
				return low + ((xVal > graph[low].x)? 1: 0);

			var mid = Math.floor((low + high)/2);
			if (xVal == graph[mid].x)
				return mid+1;

			if (xVal > graph[mid].x)
				return this.binarySearchUtil(graph, xVal,
					mid+1, high);
			return this.binarySearchUtil(graph, xVal,
				low, mid-1);
		},

		/**
			Utility functions
		*/

		formatMonth: function(month) {
			var months = [
				'Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun',
				'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'
			];
			return months[month];
		},

		getDateTime: function(time) {
			var datetime = new Date(time);
			var month = this.formatMonth(datetime.getMonth());
			var date = datetime.getDate();
			var dayHalf = (datetime.getHours()%24 < 12)? "AM": "PM";
			var hour = datetime.getHours() % 12;
			var min = datetime.getMinutes();
			var out = month + " " + date
				+ "  " + ((hour == 0)? 12: hour)
				+ ":" + ((min < 10)? '0': '') + min
				+ " " + dayHalf;
			return out;
		},

		getTimeStringHour: function(time) {
			var datetime = new Date(time);
			var hour = datetime.getHours();
			var mod = hour % 12;
			var out = ((mod == 0)? 12: mod) + " " + ((hour == 0	 || hour < 12)? "AM": "PM");
			return out;
		},

		getTimeStringDay: function(time) {
			var datetime = new Date(time);
			var month = this.formatMonth(datetime.getMonth());
			var date = datetime.getDate();
			var out = month + " " + date;
			return out;
		},

		getTimeStringWeek: function(time) {
			return this.getTimeStringDay(time);
		},

		getTimeStringMonth: function(time) {

		},

		toChartX: function(x) {
			return X_EDGE + START_X +
				(x - xFrom) * this.xScale;
		},

		toChartY: function(y) {
			return END_HEIGHT -
				(y - this.yFrom[this.mainGraph]) * this.yScale;
		},

		toGraphX: function(x) {
			return (x - X_EDGE - START_X)
				/ this.xScale + xFrom;
		},

		toGraphY: function(y) {
			return (END_HEIGHT - y)
				/ this.yScale + this.yFrom[this.mainGraph];
		},

		isPointOnChart: function(x, y, padding) {
			padding = padding || 0;
			return x > X_EDGE + START_X - padding
				&& x < END_WIDTH - START_X + padding
				&& y > Y_EDGE - padding
				&& y < END_HEIGHT + padding;
		},

		findClosestIndex: function(graphName, x) {
			// Checks by the x values b/c 1-1.
			var index = this.binarySearch(graphName, x);
			var graph = this.graphs[graphName];

			if (index == 0)
				return index;
			if (index == graph.length)
				return index-1;

			var diff1 = x - graph[index - 1].x;
			var diff2 = graph[index].x - x;
			return index - ((diff1 <= diff2)? 1: 0);
		},

		/**
			Graph methods
		**/

		initNewGraph: function(graphName) {
			// Generate a new graph at default settings
			this.graphs[graphName] = [];
			this.badBounds[graphName] = [0, 100];
			this.graphColors[graphName] = ['#46c9dd', 'rgba(70, 201, 221, 0.7)'];
			this.badColors[graphName] = ['#ff9696', 'rgba(255, 150, 150, 0.7)'];
			this.showGraphs[graphName] = false;
			this.yFrom[graphName] = 0;
			this.yTo[graphName] = 100;
			this.yDelta[graphName] = 10;
			this.yLines[graphName] =
				(this.yTo[graphName] - this.yFrom[graphName])
				/ this.yDelta[graphName];
		},

		pushPoints: function(graph, points) {
			if (this.graphs[graph] == null)
				this.initNewGraph(graph);

			for (var i = 0; i < points.length; ++i) {
				this.graphs[graph].push(points[i]);
			}
			this.redrawChart();
		},

		addPointsPlusSort: function(graph, points) {
			if (this.graphs[graph] == null)
				this.initNewGraph(graph);

			var curr = this.graphs[graph];
			for (var i = 0; i < points.length; ++i) {
				var addMeHere =
					this.binarySearch(graph, points[i].x);
				if (addMeHere < curr.length) {
					if (points[i].x >= curr[addMeHere].x)
						addMeHere++;
					curr.splice(addMeHere, 0, points[i]);
				} else {
					curr.push(points[i]);
				}
			}

			this.redrawChart();
		},

		setGraphColors: function(graphName, lineCol, filledCol) {
			this.graphColors[graphName] = [lineCol, filledCol];
			this.redrawChart();
		},

		setBadColors: function(graphName, lineCol, filledCol) {
			this.badColors[graphName] = [lineCol, filledCol];
			this.redrawChart();
		},

		setGraphBadRange: function(graphName, lower, upper) {
			this.badBounds[graphName] = [lower, upper];
			this.redrawChart();
		},

		setMainGraph: function(graph) {
			if (this.graphs[graph] != null) {
				this.yScale = (END_HEIGHT - Y_EDGE) / (this.yTo[graph]  - this.yFrom[graph] );
				this.mainGraph = graph;
			}
		},

		showGraph: function(graphName) {
			this.showGraphs[graphName] = true;
			this.redrawChart();
		},

		hideGraph: function(graphName) {
			this.showGraphs[graphName] = false;
			this.redrawChart();
		},

		changeTimeWindow: function(newTimeWindow) {
			var now = new Date();
			var to, delta;
			switch(newTimeWindow) {
				case TIME_WINDOW_DAY: // Last 24 hours
					to = new Date(Math.ceil(now.getTime()/HOUR)*HOUR);
					xDelta = HOUR;
					delta = xDelta * 25;
					break;
				case TIME_WINDOW_WEEK: // Last week
					to = new Date(now);
					to = new Date(to.setHours(23,59,59,999) + 1);
					xDelta = DAY;
					delta = xDelta * 8;
					break;
				case TIME_WINDOW_MONTH: // Last month
					to = new Date(now);
					to = new Date(to.setHours(23,59,59,999) + 1);
					xDelta = DAY * 7;
					delta = xDelta * 5;
					break;
				case TIME_WINDOW_YEAR: // Last year

					break;
				default:
					return;
					break;
			}

			xFrom = new Date(to.getTime() -
				delta).getTime();
			xTo = to.getTime();
			xLines = (xTo - xFrom) / xDelta;

			timeWindow = newTimeWindow;

			this.getTimeString =
				(timeWindow == TIME_WINDOW_DAY)? this.getTimeStringHour:
				(timeWindow == TIME_WINDOW_WEEK)? this.getTimeStringDay:
				(timeWindow == TIME_WINDOW_MONTH)? this.getTimeStringWeek:
				this.getTimeStringMonth;

			this.onResize();
		},

		changeYAxis: function(graphName, yFrom, yTo, yDelta, yLabel) {
			if (this.graphs[graphName] == null) this.initNewGraph(graphName);
			if (this.mainGraph == null) this.setMainGraph(graphName);

			this.yFrom[graphName] = yFrom;
			this.yTo[graphName] = yTo;
			this.yDelta[graphName] = yDelta;
			this.yLabel[graphName] = yLabel;
			this.yLines[graphName] = (yTo - yFrom) / yDelta;

			this.onResize();
			this.redrawChart();
		},

		/**
			Mouse events
		*/

		onPointerDownRef: function(evt) {chart.onPointerDown(evt);},
		onPointerMoveRef: function(evt) {chart.onPointerMove(evt);},
		onPointerUpRef:   function(evt) {chart.onPointerUp(evt);  },

		onPointerDown: function(evt) {
			this.pointersDown = true;
		},

		onPointerMove: function(evt) {
			try {
				var canvOffset =
					this.canvas.getBoundingClientRect();
				var x = evt.pageX - canvOffset.left;
				var y = evt.pageY - canvOffset.top;
				if (this.isPointOnChart(x,y) && this.mainGraph != null) {
					this.highlighted =
						this.findClosestIndex(this.mainGraph, this.toGraphX(x));
					this.mousePosition = {x:x, y:y};
					this.removeHighlight = true;
					this.redrawChart();
					// this.onResize();
				} else {
					this.highlighted = null;
					if (this.removeHighlight)
						this.redrawChart();
					this.removeHighlight = false;
				}

				if (this.pointersDown) {

				}

			} catch (err) {}
		},

		onPointerUp: function(evt) {
			this.pointersDown = false;
		}
	};

	chart.onLoad();

	return chart;
}



