<script>
import qs from 'qs'
import { mapGetters, mapActions } from 'vuex'
// import moment from 'moment'
import { server } from '@/js/axios.js'
import { dbSensorName } from '@/js/transforms.js'
import SensorAvatar from '@/components/SensorAvatar.vue'

export default {
  name: 'Activity',
  components: { SensorAvatar },
  data() {
    return {
      selectedSensor: [], // 'humidity', 'sunlight', 'moisture', 'temperature'
      selectedNode: [],
      nodeByGroup: [],
      view: 1,
      alert: [{
        type: 'success',
        value: true,
        content: 'This is a success alert.'
      }, {
        type: 'warning',
        value: true,
        content: 'This is a warning alert.'
      }, {
        type: 'info',
        value: true,
        content: 'This is a info alert.'
      }, {
        type: 'error',
        value: true,
        content: 'This is a error alert.'
      }],
      fab: true,
      page: 1,
      datePerPage: 3
    }
  },
  created() {},
  mounted() { if (this.getNodeByGroup) this.parseNodeByGroup() },
  beforeUpdate() { if (process.env.NODE_ENV !== 'production') console.log('before update') },
  updated() { if (process.env.NODE_ENV !== 'production') console.log('updated') },
  watch: {
    getNodeByGroup() { this.parseNodeByGroup() }
    // getNotificationsByDate () {
    //   this.expansionActiveInit = {}
    //   for (let date of Object.keys(this.getNotificationsByDate)) { this.expansionActiveInit[date] = false }
    // }
  },
  computed: {
    ...mapGetters(['getTooltipColor']),
    ...mapGetters('user', [
      'getUserData',
      'getToken'
    ]),
    ...mapGetters('user/node', [
      'getNodeInfoById',
      'getNodeByGroup',
      'getAllSensors',
      'getSensorOptions'
    ]),
    ...mapGetters('user/notification', [
      'getNotifications',
      'getNotificationsByDate',
      'getNotificationsByNode',
      'getNotificationsBySensor',
      'getNotificationsByDateAndTime',
      'getNotificationsByDateAndTimeAndNode'
    ]),
    locale() { return (navigator.language || navigator.userLanguage) },
    isLoading() {
      if (!this.getNotifications) return true
      else return false
    },
    matchRegExpr() {
      return new RegExp(this.input, 'i')
      //   return this.getUserData.nodes.filter(o => o.name.match(re))
    },
    getLengthOfPage() {
      if (!this.getNotificationsByDate) return 0
      return Math.ceil(Object.keys(this.getNotificationsByDate).length / this.datePerPage)
    }
  },
  methods: {
    ...mapActions([
      'updateAsyncSnackBar'
    ]),
    ...mapActions('user/notification', [
      'updateAsyncNotification'
    ]),
    test() {},
    // viewOneAFilter() {
    //   (selectedNode.length === 0 || selectedNode.includes(parseInt(node)))
    //
    //   (selectedSensor.length === 0 || selectedSensor.includes(noti.sensor))
    //
    //   (selectedSensor.length === 0 || selectedSensor.includes(noti.sensor)) && (selectedNode.length === 0 || selectedNode.includes(noti.nodeId))
    //
    //   return (selectedSensor.length === 0 || selectedSensor.includes(noti.sensor))
    // },
    selectedNodeFilter(nodeId) {
      if (process.env.NODE_ENV !== 'production') console.log('filter')
      return (this.selectedNode.length === 0 || this.selectedNode.includes(parseInt(nodeId)))
    },
    selectedSensorFilter(sensor) {
      return (this.selectedSensor.length === 0 || this.selectedSensor.includes(sensor))
    },
    getSensorTypeColor(type) {
      if (type === 'humidity') return 'purple accent-2'
      else if (type === 'moisture') return 'blue accent-2'
      else if (type === 'temperature') return 'deep-orange accent-2'
      else if (type === 'sunlight') return 'yellow accent-2'
    },
    dbSensorName: dbSensorName,
    pageFilter(i) {
      return !(Math.floor(i / this.datePerPage) !== (this.page - 1))
    },
    updateNotification(notis, dismissed) { // noti is an array
      this.updateAsyncSnackBar({
        colorType: 'info',
        mode: 'vertical',
        show: true,
        content: `${(dismissed) ? 'Dismiss' : 'Undismiss'} ${(notis.length > 1) ? 'Multiple' : ''} Notification${(notis.length > 1) ? 's' : ''}`
      })
      const ids = []
      for (let n of notis) { if (n.dismissed !== dismissed) ids.push(n.id) }
      let count = 0
      while (count <= ids.length) {
        if (ids.slice(count, count + 20).length === 0) break
        this.sentTimeoutUpdateNotification(ids, count, dismissed)
        count += 10
      }
    },
    sentTimeoutUpdateNotification(ids, count, dismissed) {
      setTimeout(() => {
        server.put(`/notifications?notifications=${ids.slice(count, count + 20).join()}`, qs.stringify({ dismissed: dismissed }))
          .then((res) => { for (let n of res.data) { this.updateAsyncNotification(n) } })
          .catch(err => console.trace(err))
      }, count * 20)
    },
    parseNodeByGroup() {
      this.nodeByGroup = []
      for (let groupName of Object.keys(this.getNodeByGroup)) {
        this.nodeByGroup.push({ header: groupName })
        for (let node of this.getNodeByGroup[groupName]) { this.nodeByGroup.push(node) }
      }
    }
    // getAllNotifications () {
    //   server.get(`/notifications/all`)
    //       .then((res) => { this.alert = res.data })
    // }
  }
}
</script>

