import ROUTER from '@/router'

export const setActivePage = (state, payload) => {
  if (process.env.NODE_ENV !== 'production') console.log(`setActivePage: ${payload.path}`)
  if (!payload.name) return
  ROUTER.push(payload)
  state.activePage = payload.name // Using the first node as default
}
export const setSelectedNodeId = (state, payload) => {
  if (process.env.NODE_ENV !== 'production') console.log('setSeletedNodeId')
  // ROUTER.push({ name: 'dashboard', params: { nodeId: payload } })
  state.selectedNodeId = payload // Using the first node as default
}
export const updateSnackBar = (state, config) => {
  // if (config.timeout) state.snackBar.timeout = config.timeout
  // if (config.horizontalPos) state.snackBar.horizontalPos = config.horizontalPos
  // if (config.verticalPos) state.snackBar.verticalPos = config.verticalPos
  // if (config.colorType) state.snackBar.colorType = config.colorType
  // if (config.mode) state.snackBar.mode = config.mode
  // if (config.show) state.snackBar.show = config.show
  // if (config.content) state.snackBar.content = config.content
  state.snackBar = Object.assign({}, state.snackBar, config)
}
export const setMini = (state, payload) => { state.mini = payload }
export const setDrawer = (state, payload) => { state.drawer = payload }
export const setWelcomeDialog = (state, payload) => { state.welcomeDialog = payload }
export const setConfigureNodeDialog = (state, payload) => { state.configureNodeDialog = payload }
export const setAccountSettingDialog = (state, payload) => { state.accountSettingDialog = payload }
export const setTooltipColor = (state, payload) => { state.tooltipColor = payload }



