import Cookies from 'js-cookie'

export const logout = () => {
  if (process.env.NODE_ENV !== 'production') console.log('logout')
  Cookies.remove('token')
  window.location.href = '/'
}

export const errorHandler = (err) => {
  switch (err.response.status) {
    case 401:
      if (process.env.NODE_ENV !== 'production') console.log(401)
      alert('Session might have timeout, please login again.')
      logout()
      break
    case 404:
      if (process.env.NODE_ENV !== 'production') console.log(404)
    //   logout()
      break
    default:
      alert('Something bad might have happen to the application, try refreshin the page.')
    //   alert('Session might have timeout, please login again.')
    //   logout()
  }
  return err.response.status
}



