<!-- =========================== -->
<!-- 9/17/2017 Template by Ewing -->
<!-- =========================== -->

<template>
<v-list class="pt-0"
        dense>
  <v-list-group>
    <v-list-tile slot="item">
      <v-tooltip right
                 :color="getTooltipColor">
        <v-list-tile-action slot="activator">
          <v-badge color="'#424242'">
            <!-- <v-icon class="text--lighten-1  orange--after">{{ icon }}</v-icon> -->
            <label class="switch"
                   @click.stop>
              <input type="checkbox" v-model="mode" checked>
              <span class="custom-slider round"></span>
            </label>
          </v-badge>
        </v-list-tile-action>
        <span>Toggle Groups & All Devices</span>
      </v-tooltip>
      <v-list-tile-content>
        <v-list-tile-title class="text--lighten-2">
          {{ title }}
        </v-list-tile-title>
      </v-list-tile-content>
      <v-list-tile-action v-if="getNodeByName">
        <v-icon>keyboard_arrow_down</v-icon>
      </v-list-tile-action>
    </v-list-tile>

    <div v-if="getNodeByName && mode"
         class="node_selection_side_bar">
      <v-list-tile v-for="(node, j) in getNodeByName"
                   :key="j"
                   @mouseover="hoveredOnTile = node.id"
                   @mouseleave="hoveredOnTile = -1"
                   @click="setAsyncSelectedNodeId(node.id); setAsyncActivePage({name: 'dashboard', params: { nodeId: node.id }})">
        <v-list-tile-content>
          <v-list-tile-title class="text--lighten-2"
                             :class="{'teal--text': node.id == getSelectedNodeId, 'white--text': !node.active}">
            <transition name="slide-fade-left-xs"
                        mode="out-in">
              <v-icon class="editIcon"
                      v-if="hoveredOnTile === node.id && isEditing !== node.id"
                      @click.stop="isEditing = node.id">edit</v-icon>
              <v-icon class="editIcon"
                      v-else-if="hoveredOnTile === node.id && isEditing === node.id"
                      @click.stop="isEditing = -1; changeNodeName(editNodeName, node.id)">save</v-icon>
            </transition>
            <span v-if="isEditing === node.id">
              <input type="text"
                     spellcheck="false"
                     name="FirstName"
                     class="editInput"
                     v-model="editNodeName"
                     @click.stop><br>
            </span>
            <span v-else>
              <span v-if="node.name === emptyNodeNameString(node.id)"
                    class="deviceNameFont">{{ node.name }}</span>
              <span v-else>
                <span class="deviceNameFont">{{ node.name }}</span>:
                <span class="deviceIdFont">{{ node.id }}</span>
              </span>
            </span>
          </v-list-tile-title>
        </v-list-tile-content>
        <v-list-tile-action>
          <deviceBattery :nodeID="node.id.toString()"></deviceBattery>
        </v-list-tile-action>
      </v-list-tile>
      <v-list-tile @click="setAsyncConfigureNodeDialog(true)">
        <v-list-tile-content>
          <v-list-tile-title>
            <v-icon medium
                    class="white--text">add_circle</v-icon>
            Add Device
          </v-list-tile-title>
        </v-list-tile-content>
      </v-list-tile>
    </div>
    <div v-if="getNodeByGroup && !mode"
         class="node_selection_side_bar">
      <v-list class="pt-0"
              dense>
        <v-list-group v-for="(group, i) in Object.keys(getNodeByGroup)"
                      :key="i">
          <v-list-tile slot="item">
            <v-list-tile-content>
              <v-list-tile-title>
                <span>{{group}}</span>
                <span class="custom-badge teal white--text px-1">{{getNodeByGroup[group].length}}</span>
              </v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-action>
              <v-icon>keyboard_arrow_down</v-icon>
            </v-list-tile-action>
          </v-list-tile>
          <v-list-tile v-for="(node, j) in getNodeByGroup[group]"
                       :key="j"
                       @mouseover="hoveredOnTile = node.id"
                       @mouseleave="hoveredOnTile = -1"
                       @click="setAsyncSelectedNodeId(node.id); setAsyncActivePage({name: 'dashboard', params: { nodeId: node.id }})">
            <v-list-tile-content>
              <v-list-tile-title class="text--lighten-2"
                                 :class="{'teal--text': node.id == getSelectedNodeId, 'white--text': !node.active}">
                <transition name="slide-fade-left-xs"
                            mode="out-in">
                  <v-icon class="editIcon"
                          v-if="hoveredOnTile === node.id && isEditing !== node.id"
                          @click.stop="isEditing = node.id">edit</v-icon>
                  <v-icon class="editIcon"
                          v-else-if="hoveredOnTile === node.id && isEditing === node.id"
                          @click.stop="isEditing = -1; changeNodeName(editNodeName, node.id)">save</v-icon>
                </transition>
                <span v-if="isEditing === node.id">
                  <input type="text"
                         spellcheck="false"
                         name="FirstName"
                         class="editInput"
                         v-model="editNodeName"
                         @click.stop><br>
                </span>
                <span v-else>
                  <span v-if="node.name === emptyNodeNameString(node.id)">{{ node.name }}</span>
                  <span v-else>{{ node.name }}: {{ node.id }}</span>
                </span>
              </v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-action>
              <deviceBattery :nodeID="node.id.toString()"></deviceBattery>
            </v-list-tile-action>
          </v-list-tile>
        </v-list-group>
      </v-list>
    </div>
  </v-list-group>
  <v-divider></v-divider>
</v-list>
</template>

<script>
import {
  mapGetters,
  mapActions
} from 'vuex'
import deviceBattery from '@/components/DeviceBattery'
import { emptyNodeNameString } from '@/js/transforms.js'

export default {
  name: 'NavigationDrawer',
  components: { deviceBattery },
  data() {
    return {
      hoveredOnTile: -1,
      isEditing: -1,
      editNodeName: '',
      title: 'Device',
      icon: 'list',
      activePath: '',
      displayMode: true,
      mode: true
    }
  },
  created() { if (process.env.NODE_ENV !== 'production') console.log('NavigationDrawer.vue') },
  computed: {
    ...mapGetters([
      'getSelectedNodeId',
      'getActivePage',
      'getTooltipColor',
      'getMobileBreakPoint',
      'getViewport'
    ]),
    ...mapGetters('user', ['getUserData']),
    ...mapGetters('user/node', ['getNodeInfoById', 'getNodeByGroup', 'getNodeByName'])
  },
  methods: {
    ...mapActions([
      'setAsyncSelectedNodeId',
      'setAsyncActivePage',
      'setAsyncConfigureNodeDialog'
    ]),
    ...mapActions('user/node', ['updateAsyncNodesInfo']),
    emptyNodeNameString,
    changeNodeName(newName, nodeId) {
      if (this.getNodeInfoById(nodeId).name !== newName) this.updateAsyncNodesInfo({ nodes: [nodeId], value: { name: newName } })
    }
  },
  watch: {
    isEditing(val) { if (val !== -1) this.editNodeName = this.getNodeInfoById(val).name }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="sass" scoped>
.border
  // border: 1px dashed grey

.node_selection_side_bar
  height: 300px
  overflow: hidden
  overflow-y: scroll

.editIcon
  padding-left: 0
  transition: all 0.4s ease
  cursor: default
  font-size: 20px
  &:hover
    color: #55dd97
    cursor: pointer

.deviceNameFont
  font-weight: 900

.deviceIdFont
  font-weight: 100

.custom-badge
  background-color: #22af65
  color: #fff
  font-size: 10px
  text-align: center
  border-radius: 50%

// https://www.w3schools.com/howto/tryit.asp?filename=tryhow_css_switch\
$switchWidth: 22px
$switchHeight: 16px
$swichtColor: #2196F3
$swichtBGC: #ccc
$sliderWidth: 12px
$sliderHeight: 12px
$gapSize: 2px

.switch
  position: relative
  display: inline-block
  width: $switchWidth
  height: $switchHeight

.switch input
  display: none

.custom-slider
  position: absolute
  cursor: pointer
  top: 0
  left: 0
  right: 0
  bottom: 0
  background-color: $swichtBGC
  -webkit-transition: .4s
  transition: .4s

.custom-slider:before
  position: absolute
  content: ""
  height: $sliderHeight
  width: $sliderWidth
  left: $gapSize
  bottom: $gapSize
  background-color: white
  -webkit-transition: .4s
  transition: .4s

input:checked + .custom-slider
  background-color: $swichtColor

input:focus + .custom-slider
  box-shadow: 0 0 1px $swichtColor

input:checked + .custom-slider:before
  -webkit-transform: translateX(calc(#{$sliderWidth} - 6px))
  -ms-transform: translateX(calc(#{$sliderWidth} - 6px))
  transform: translateX(calc(#{$sliderWidth} - 6px))

/* Rounded sliders */
.custom-slider.round
  border-radius: 34px

.custom-slider.round:before
  border-radius: 50%

input
  border-bottom: 1px solid white
  height: 18px
  outline: none

.slide-fade-left-xs-enter
  transform: translateX(-10px)
  opacity: 0
.slide-fade-left-xs-enter-active
  transition: all 0.8s ease
.slide-fade-left-xs-enter-to
  transform: translateX(0px)
  opacity: 1
.slide-fade-left-xs-leave
  transform: translateX(0px)
  opacity: 1
.slide-fade-left-xs-leave-active
  transition: all 0.2s
.slide-fade-left-xs-leave-to
  transform: translateX(-10px)
  opacity: 0

</style>



