import 'vuetify/dist/vuetify.min.css'
import '@/style/material_icon.css'
// import 'reset-css/reset.css'
import 'font-awesome/css/font-awesome.min.css'

import Vue from 'vue'
import App from '@/App.vue'
import Vuetify from 'vuetify'
import router from '@/router'
import store from '@/store'
import Cookies from 'js-cookie'
import { capitalize } from '@/js/transforms'
// import Vue2Filters from 'vue2-filters'

// Check if token exist here
const token = Cookies.get('token')
if (!token) {
  if (process.env.NODE_ENV !== 'production') {
    Cookies.set('token', prompt(), {expires: 1 / (24)})
  } else window.location.href = '/login'
} else {
  Vue.use(Vuetify)
  Vue.filter('capitalize', capitalize) // Vue.use(Vue2Filters)

/* eslint-disable no-new */
  new Vue({
    el: '#app',
    store,
    router,
    render: h => h(App),
    created () {
      if (process.env.NODE_ENV !== 'production') console.log('Created')
      this.$store.dispatch('user/setAsyncUserData')
      this.$store.dispatch('user/notification/setAsyncNotifications')
      if (process.env.NODE_ENV !== 'production') console.log('Created End')
    }
  })
}



