<template>
<v-dialog v-model="showDialog"
          lazy
          absolute
          persistent
          transition="dialog-bottom-transition"
          :overlay="false">
  <v-card>
    <v-toolbar dark
               class="primary">
      <v-btn icon
             @click.native="setAsyncAccountSettingDialog(false)"
             dark>
        <v-icon>close</v-icon>
      </v-btn>
      <v-toolbar-title>Settings</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn dark
               flat
               @click.native="setAsyncAccountSettingDialog(false)">Save</v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <v-list three-line
            subheader>
      <v-subheader>User Controls</v-subheader>
      <v-list-tile avatar>
        <v-list-tile-content>
          <v-list-tile-title>Content filtering</v-list-tile-title>
          <v-list-tile-sub-title>Set the content filtering level to restrict appts that can be downloaded</v-list-tile-sub-title>
        </v-list-tile-content>
      </v-list-tile>
      <v-list-tile avatar>
        <v-list-tile-content>
          <v-list-tile-title>Password</v-list-tile-title>
          <v-list-tile-sub-title>Require password for purchase or use password to restrict purchase</v-list-tile-sub-title>
        </v-list-tile-content>
      </v-list-tile>
    </v-list>
    <v-divider></v-divider>
    <v-list three-line
            subheader>
      <v-subheader>General</v-subheader>
      <v-list-tile avatar>
        <v-list-tile-action>
          <v-checkbox v-model="notifications"></v-checkbox>
        </v-list-tile-action>
        <v-list-tile-content @click="notifications = !notifications"
                             style="cursor: pointer;">
          <v-list-tile-title>Notifications</v-list-tile-title>
          <v-list-tile-sub-title>Notify me about updates to apps or games that I downloaded</v-list-tile-sub-title>
        </v-list-tile-content>
      </v-list-tile>
    </v-list>
  </v-card>
</v-dialog>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
// import { server } from '@/js/axios.js'

export default {
  name: 'AccountSetting',
  components: {},
  props: {
    templateProp: { type: String, default: 'Template' }
  },
  data() {
    return {
      showDialog: this.getAccountSettingDialog,
      notifications: true
    }
  },
  created() {},
  mounted() {},
  beforeDestory() {},
  watch: {
    getAccountSettingDialog(val) { this.showDialog = val },
    showDialog(val) { this.setAsyncAccountSettingDialog(val) }
  },
  computed: {
    ...mapGetters(['getAccountSettingDialog'])
  },
  methods: {
    ...mapActions(['setAsyncAccountSettingDialog']),
    test() {}
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style lang="sass" scoped>
.border
    border: 1px dashed grey
</style>



