<template>
<v-card class="white lighten-5 border mb-1">
  <v-card-text class="sensorBox border pt-1 px-0"
               :class="{ isEditing: !valid }">
    <v-layout row
              wrap
              class="border">
      <v-flex xs12
              class="border text-xs-left subheading pa-0">
        <v-layout row
                  wrap
                  class="border">
          <v-flex xs8
                  class="border px-2">
            <SensorAvatar :sensor="sensorType"
                          :size="'20px'"></SensorAvatar>
            <span name="box-title">{{displayName | capitalize}}</span>
          </v-flex>
          <v-flex xs4>
            <v-layout row
                      wrap
                      class="text-xs-right">
              <v-flex xs4
                      class="border icon"></v-flex>
              <v-flex xs4
                      class="border icon"></v-flex>
              <v-flex xs4
                      class="border px-2">
                <v-icon class="border"
                        style="font-size: 20px;"
                        name="status-btn"
                        :class="healthColorClass">
                  radio_button_checked
                </v-icon>
              </v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-flex>
      <v-flex xs12
              class="border display-1 text-xs-center headline"
              name="data-block"
              :class="healthColorClass">
        <!-- {{latestReading}} -->
        <span v-if="latestReading == '_no_data_found' || nodeId == 'no_node_selected' || !sensorType"
              name="_no_data_found">
          No Data
        </span>
        <span v-else-if="latestReading == '_loading'"
              name="_loading">
          <v-progress-circular indeterminate
                               v-bind:size="28"
                               :color="getSensorOptions[sensorType].colorName || 'grey'"></v-progress-circular>
        </span>
        <span v-else
              name="latest_reading">
          <i-odometer class="iOdometer"
                      :value="latestReading"></i-odometer>
          <span v-html="unit"
                name="reading-unit"
                style="position:relative; top: 4px;"></span>
        </span>
      </v-flex>
      <v-flex xs12
              class="border pa-0">
        <div v-if="!isEditing"
             name="view-mode"
             class="infoBox pa-0 grey lighten-5 border">
          <v-layout row
                    wrap
                    class="border innerInfoBox pa-0"
                    name="inner-info-box"
                    :class="healthColorClass">
            <v-flex xs12
                    class="subheading text-xs-left border pa-0 ml-4"
                    style="font-weight: 600">Ideal</v-flex>
            <v-flex xs10
                    offset-xs1
                    class="title text-xs-center border"
                    style="font-weight: 600">
              <span v-if="nodeId == 'no_node_selected'">
                <i-odometer class="iOdometer"
                            :value="0"></i-odometer>
              </span>
              <span v-else-if="!sensorType"> x </span>
              <span v-else>
                <i-odometer class="iOdometer"
                            :value="parseInt(idealMin)"></i-odometer>
              </span>
              -
              <span v-if="nodeId == 'no_node_selected'">
                <i-odometer class="iOdometer"
                            :value="0"></i-odometer>
              </span>
              <span v-else-if="!sensorType"> x </span>
              <span v-else>
                <i-odometer class="iOdometer"
                            :value="parseInt(idealMax)"></i-odometer>
              </span>
              <span v-if="sensorType"
                    v-html="unit"
                    name="ideal-unit"
                    style="position:relative; top: 2px;"></span>
            </v-flex>
            <v-flex xs1>
              <v-icon class="icon"
                      v-if="sensorType"
                      name="edit-btn"
                      @click.stop="isEditing = true">edit</v-icon>
            </v-flex>
          </v-layout>
        </div>
        <div v-else
             name="edit-mode"
             class="px-2 infoBox grey lighten-5 border">
          <v-form v-model="valid"
                  ref="form"
                  lazy-validation>
            <v-layout>
              <v-flex xs5
                      class="px-1">
                <v-text-field name="Min"
                              label="Min"
                              v-model="editMin"
                              :suffix="unit"
                              :rules="parseRules(actionValidation['min'])"></v-text-field>
              </v-flex>
              <v-flex xs5
                      class="px-1">
                <v-text-field name="Max"
                              label="Max"
                              v-model="editMax"
                              :suffix="unit"
                              :rules="parseRules(actionValidation['max'])"></v-text-field>
              </v-flex>
              <v-flex xs2
                      class="pt-1 body-1">
                <div class="indigo lighten-4 white--text text-xs-center edit-btn mb-1"
                     name="save-btn"
                     @click.stop="updateOneNodeInfo()">Save</div>
                <div class="blue lighten-4 white--text text-xs-center edit-btn mb-1"
                     name="cancel-btn"
                     @click.stop="closeEditing()">Cancel</div>
              </v-flex>
            </v-layout>
          </v-form>
        </div>
      </v-flex>
    </v-layout>
  </v-card-text>
</v-card>
</template>

<script>
import IOdometer from 'vue-odometer'
import SensorAvatar from '@/components/SensorAvatar.vue'
import { mapGetters, mapActions } from 'vuex'
import { dbSensorName } from '@/js/transforms.js'
import { Theme } from '@/style'

export default {
  name: 'sensorBox',
  components: { IOdometer, SensorAvatar },
  props: {
    nodeId: { type: String, required: true },
    sensorType: { type: String, default: '' }
  },
  data() {
    return {
      isEditing: false,
      editMin: '',
      editMax: '',
      valid: true,
      rules: {
        // required: (value) => {
        //   if (value === '') return 'Required.'
        //   else return true
        // },
        isBetween: (val) => { return this.minMaxCheck(this.editMin, this.editMax) },
        isInRange: (val) => {
          if ((parseInt(val) < 0) || (parseInt(val) > 100)) return 'Value not in range.'
          else return true
        },
        isValidInt: (val) => {
          if (!this.isNumber(val) && val !== this.multiNodesText && val !== this.disableText) return 'Input an Integer.'
          else return true
        }
      },
      actionValidation: {
        'min': ['isBetween', 'isInRange', 'isValidInt'],
        'max': ['isBetween', 'isInRange', 'isValidInt']
      }
    }
  },
  created() {},
  watch: {
    isEditing() {
      this.editMin = this.idealMin
      this.editMax = this.idealMax
    }
  },
  computed: {
    ...mapGetters(['getTooltipColor']),
    ...mapGetters('user/node', ['getNodeInfoById', 'getSensorOptions', 'getLatestReadings']),
    selectedNode() { return this.getNodeInfoById(this.nodeId) },
    unit() { return (this.sensorType ? this.getSensorOptions[this.sensorType].unit : '-') },
    displayName() { return (this.sensorType ? this.getSensorOptions[this.sensorType].name : '---') },
    idealMin() { return this.selectedNode[`${dbSensorName(this.sensorType)}Min`] },
    idealMax() { return this.selectedNode[`${dbSensorName(this.sensorType)}Max`] },
    isHealthy() { return (this.latestReading <= this.idealMax) && (this.latestReading >= this.idealMin) },
    healthColorClass() {
      if (this.latestReading === '_loading') return Theme.loadingFontColorName
      else if (this.latestReading === '_no_data_found') return Theme.inactiveFontColorName
      else if (this.isHealthy) return Theme.goodFontColorName
      else return Theme.badFontColorName
    },
    latestReading() {
      if (!this.getLatestReadings) return '_loading'
      else if (!(this.nodeId in this.getLatestReadings)) return '_loading'
      else if (!this.getLatestReadings[this.nodeId].latest) return '_no_data_found'
      else if (!this.getLatestReadings[this.nodeId].latest[this.sensorType]) return '_no_data_found'
      else return this.getLatestReadings[this.nodeId].latest[this.sensorType]
    }
  },
  methods: {
    ...mapActions('user/node', ['updateAsyncNodesInfo']),
    updateOneNodeInfo() {
      if (this.$refs.form.validate()) {
        this.updateAsyncNodesInfo({
          nodes: [this.nodeId],
          value: {
            [`${dbSensorName(this.sensorType)}Min`]: this.editMin,
            [`${dbSensorName(this.sensorType)}Max`]: this.editMax
          }
        })
        this.closeEditing()
      }
    },
    closeEditing() {
      this.isEditing = false
      this.valid = true
    },
    minMaxCheck(min, max) {
      if (parseInt(min) > parseInt(max)) return 'Min > Max.'
      else return true
    },
    parseRules(list) {
      let rules = []
      for (let r of list) { rules.push(this.rules[r]) }
      return rules
    },
    isNumber(val) {
      const re = new RegExp(/\d*/)
      /* eslint-disable eqeqeq */
      if (parseInt(String(val).match(re)[0]) != val) return false
      else return true
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped lang="sass">
.border
  // border: 1px dashed grey

.infoBox
  cursor: default
  height: 55px
  margin: 0 auto
  width: 98%
  .innerInfoBox
    transition: all 0.1s ease

.icon
  padding-left: 0
  transition: all 0.4s ease
  cursor: default
  font-size: 20px
  &:hover
    color: #55dd97
    cursor: pointer

.nodeId
  cursor: default
  font-weight: 600
  color: #bEbEbE

.sensorBox
  cursor: default
  height: 120px
  box-shadow: 0px 1px 1px #BBBBBB
  transition: all 0.5s ease
  &:hover
    box-shadow: 3px 3px 4px #888888

.isEditing
  height: 150px !important

.edit-btn
  border-radius: 2px
  transition: all 0.3s ease
  cursor: pointer
  &:hover
    box-shadow: 0px 2px 3px #a7a7a7

</style>



